<template>
  <ul>
    <tree-node v-for="node of treeData" :key="node.id" class="item"
      :node.sync="node"
      :onlyLeafCanSelect="onlyLeafCanSelect"
      :selectedNodes="selectedNodes"
      :mustSelectOne="mustSelectOne"
      :multiSelect="multiSelect"></tree-node>
  </ul>
</template>

<script>
import TreeNode from './tree-node'

export default {
  name: 'pad-tree',
  props: {
    treeData: Array,

    // 可以选中多个节点
    multiSelect: { type: Boolean, default: false },

    // 只能选择叶子节点
    onlyLeafCanSelect: { type: Boolean, default: true },

    // 必须有一个节点是选中的，即不能取消选择
    mustSelectOne: { type: Boolean, default: false },

    // 初始化的时候如果没有设置的节点，是否将第一个叶子节点选中
    initFirstSelected: { type: Boolean, default: false },

    // openLevel 默认展开的层级
    openLevel: { type: Number, default: 1 },

    selectedNodes: { type: Array, required: true },

  },
  data() {
    return {}
  },
  mounted() {
    if (this.initFirstSelected) {
      this.travelFirstLeafNode(this.treeData)
    }
    if (this.openLevel > 1) {
      this.openNode(this.treeData, 1, this.openLevel)
    }
  },
  components: { TreeNode },
  watch: {
    treeData() {
      if (this.initFirstSelected) {
        this.travelFirstLeafNode(this.treeData)
      }
      if (this.openLevel > 1) {
        this.openNode(this.treeData, 1, this.openLevel)
      }
    },
  },
  methods: {
    openNode(nodes, curLevel, targetLevel) {
      for (const node of nodes) {
        if (curLevel < targetLevel && node.children && node.children.length) {
          this.openNode(node.children, curLevel + 1, targetLevel)
          node.expanded = true
        }
      }
    },
    travelFirstLeafNode(nodes) {
      for (const node of nodes) {
        if (node.children && node.children.length) {
          node.expanded = true
          return this.travelFirstLeafNode(node.children)
        }
        node.selected = true
        this.selectedNodes.push(node)
        return
      }
    },
  },
}

</script>

<style lang="less" scoped>
ul {
  padding: .5rem;
  margin: 0;
}
</style>



