<template>
  <Modal class="formula-editor modal-massive" @close="$emit('close')" :withFooter="false">
    <h3 slot="header">编辑公式</h3>
    <template slot="body">
      <div class="common-usage" id="fcommon-usage">
        <div class="common-symbol">
          <template v-for="symbolCollection of mathSymbols">
            <h4>{{symbolCollection.name}}</h4>
            <div>
              <span class="symbol-item"
                v-for="symbol of symbolCollection.collection" :title="symbol.latex"
                @click="insertLatex(symbol.latex)">\({{symbol.latex}}\)</span>
            </div>
          </template>
        </div>
        <div class="common-formula">
          <div class="pill-filter">
            <div class="filter-label " v-for="formulaCollection of mathFormulas"
              :class="formulaGroupSelected(formulaCollection.groupId)?'active':''"
              @click="toggleFormulaGroup(formulaCollection.groupId)">
              <span class="formula">\({{formulaCollection.latex}}\)</span>
              <span class="formula-text">{{formulaCollection.name}}</span>
            </div>
          </div>
          <div v-for="formulaCollection of mathFormulas" v-show="formulaGroupSelected(formulaCollection.groupId)">
            <span class="formula-item"
              v-for="formula of formulaCollection.collection" :title="formula.latex"
              @click="insertLatex(formula.latex)"> \({{formula.latex}}\) </span>
            <p class="text-muted" v-if="formulaCollection.groupId === 8">可视化输入不支持矩阵类型的公式, 请用latex直接输入</p>
          </div>
        </div>
      </div>
      <div class="edit-area">
        <div class="ui secondary pointing blue menu">
          <a :class="isLatexInput?'active':''" @click="typeMethod=2" class="item">Latex输入</a>
          <a :class="isQuillInput?'active':''" @click="typeMethod=1" class="item">可视化输入</a>
          <div class="right menu">
            <a class="ui item" href="/pad/support/formula-editor.md" target="_blank"><i class="help circle outline icon"></i>公式录入说明</a>
          </div>

        </div>

        <div v-show="isQuillInput">
          <div>输入公式：</div>
          <div id="math-field-animated"></div>
        </div>
        <div v-show="isLatexInput">
          <div>输入latex:</div>
          <textarea class="formula-latex-input" id="formula-latex-input" rows="6" v-model="latextyped">
          </textarea>
        </div>
        <button class="ui small primary button" @click="saveFormula()">
          保存
        </button>
        <button class="ui small button" @click="$emit('close')">
          取消
        </button>
        <div v-show="isLatexInput">
          <p>公式渲染结果：</p>
          <span class="formula latex-typed-render" id="latex-typed-render"></span>
        </div>
      </div>
    </template>
  </Modal>
</template>

<script>
import Modal from 'components/modal'
import { MATH_SYMBOLS } from 'assets/js/math/symbols'
import { MATH_FORMULAS } from 'assets/js/math/formula'

export default {
  name: 'formula-editor',
  placeholder: { default: '输入...' },
  data() {
    return {
      mathField: null,
      mathSymbols: MATH_SYMBOLS,
      mathFormulas: MATH_FORMULAS,
      formulaGids: [1, 2, 8],
      typeMethod: 2,
      latextyped: '',
    }
  },
  components: { Modal },
  mounted() {
    const htmlElement = document.getElementById('math-field-animated')
    const config = { restrictMismatchedBrackets: true }
    this.mathField = MQ.MathField(htmlElement, config)
    this.$nextTick(() => {
      renderMathInElement(
        document.getElementById('fcommon-usage'),
        {
          delimiters: [
            { left: '$$', right: '$$', display: true },
            { left: '\\[', right: '\\]', display: true },
            { left: '$', right: '$', display: false },
            { left: '\\(', right: '\\)', display: false },
          ],
        },
      )
      // MathJax.Hub.Queue(['Typeset', MathJax.Hub])
    })
  },
  computed: {
    isQuillInput() {
      return this.typeMethod === 1
    },
    isLatexInput() {
      return this.typeMethod === 2
    },
  },
  watch: {
    typeMethod(val) {
      if (val === 2) {
        this.latextyped = this.mathField.latex()
      } else if (val === 1) {
        this.$nextTick(function () {
          this.mathField.latex(this.latextyped)
        })
      }
    },
    latextyped() {
      document.getElementById('latex-typed-render').innerHTML = `\\(${this.latextyped}\\)`
      this.$nextTick(function () {
        MathJax.Hub.Queue(['Typeset', MathJax.Hub])
      })
    },
  },
  methods: {
    generateInnerLatex(latex) {
      this.$nextTick(function () {
        MathJax.Hub.Queue(['Typeset', MathJax.Hub])
      })
      return `\\(${latex}\\)`
    },
    saveFormula() {
      if (this.isQuillInput) {
        this.$emit('updateFormula', this.mathField.latex())
      } else if (this.isLatexInput) {
        this.$emit('updateFormula', this.latextyped)
      }
    },
    insertLatex(latexString) {
      if (this.isQuillInput) {
        this.mathField.write(latexString)
      } else if (this.isLatexInput) {
        const cursor = $('#formula-latex-input').prop('selectionStart')
        this.latextyped = [this.latextyped.slice(0, cursor), latexString, this.latextyped.slice(cursor)].join('')
      }
    },
    formulaGroupSelected(groupId) {
      return this.formulaGids.indexOf(groupId) >= 0
    },
    toggleFormulaGroup(groupId) {
      const index = this.formulaGids.indexOf(groupId)
      if (index >= 0) {
        this.formulaGids.splice(index, 1)
      } else {
        let i = 0
        for (; i < this.formulaGids.length; i++) {
          if (this.formulaGids[i] > groupId) {
            break
          }
        }
        this.formulaGids.splice(i, 0, groupId)
      }
      this.$nextTick(function () {
        MathJax.Hub.Queue(['Typeset', MathJax.Hub])
      })
    },
  },
}
</script>
<style lang="less">
  @formula-font: 'Microsoft YaHei', 'Hiragino Sans GB', 'WenQuanYi Micro Hei', STXihei, SimHei, sans-serif;

  .formula-editor.modal-massive .modal-body {
    padding: 0;
    display: flex;
    min-height: 80vh;
    max-height: 90vh;
  }
  #math-field-animated {
    height: 8em;
    width: 100%;
  }
  .edit-area {
    flex: 1;
    border-left: solid 1px #ddd;
    padding: 1em 1.5em;
    .ui.button {
      margin-top: .5em;
      float: right;
    }

    .mq-editable-field, .mq-math-mode .mq-editable-field {
      border: 1px solid #aaa;
      border-radius: .3em;
      padding: .3em;
      &.mq-focused {
        box-shadow: none;
        border: solid 1px #85b7d9;
      }
    }
    .formula-latex-input {
      font-family: @formula-font;
      font-size: 15px;
      border: 1px solid #aaa;
      border-radius: .3em;
      padding: .3em;
      width: 100%;
      outline: none;
      &:hover, &:focus, &:active {
        box-shadow: none;
        border: solid 1px #85b7d9;
      }
    }
    .latex-typed-render.formula {
      font-size: 16px;
      font-family: @formula-font;
    }
  }
  .common-usage {
    flex: 1;
    display: flex;
    flex-direction: column;
    max-width: 500px;
    h4 {
      font-size: 12px;
      margin: .6em -10px .2em;
      padding: .2em .2em .2em 1em;
      background-color: #f9f9f9;
    }
    .symbol-item, .formula-item {
      text-align: center;
      font-weight: bold;
      display: inline-block;
      line-height: 1.8em;
      background-color: #f9f9f9;
      border: solid 1px #ccc;
      margin: 4px;
      cursor: pointer;
      border-radius: 2px;

      &:hover, &:focus, &:active {
        border: solid 1px transparent;
        box-shadow: 0px 0px 2px 1px #42A5F5;
      }
    }
    .common-symbol, .common-formula {
      width: 100%;
      overflow-y: scroll;
      padding: 10px;
    }
    .common-symbol {
      border-bottom: solid 1px #aaa;
      min-height: 40%;
      max-height: 40%;
      .symbol-item {
        width: 1.9em;
        height: 1.9em;
        .katex {
          font-size: 14px;
        }
      }
    }
    .common-formula {
      flex: 1;
      .pill-filter {
        background-color: #f0f0f0;
        margin: -10px -10px 10px -10px;
        padding: 0 1em .5em 1em;
        .filter-label {
          min-width: 5em;
          min-height: 2em;
          line-height: 2em;
          text-align: center;
          margin-top: .5em;
          display: inline-block;
        }
        .filter-label.active {
          pointer-even  ts: auto;
        }
        .formula-text {
          display: inline-block;
          max-width: 3em;
          font-size: 12px;
          line-height: 1em;
          vertical-align: middle;
        }
      }
      .formula-item {
        vertical-align: bottom;
        padding: .5em;
        min-width: 3em;
        .katex {
          font-size: 18px;
        }
      }
    }
  }
</style>



