<template>
  <nav v-if="totalPage > 1" :class="size">
    <ul>
      <li :class="curPage <= 1?'disabled':''"
        @click="$emit('update:selected', curPage-1)">
        <icon name="angle-left"></icon>
      </li>
      <li v-for="page in pageGroups.first" @click="$emit('update:selected', page)" :key="page">
        <span>{{page}}</span>
      </li>
      <template v-if="pageGroups.first.length > 0">
        ..
      </template>
      <li v-for="page in pageGroups.middle" :class="curPage === page?'active':''"  :key="page"
        @click="$emit('update:selected', page)">
        <span>{{page}}</span>
      </li>
      <template v-if="pageGroups.last.length > 0">
        ..
      </template>
      <li v-for="page in pageGroups.last"  :key="page"
        @click="$emit('update:selected', page)">
        <span>{{page}}</span>
      </li>

      <li :class="curPage >= totalPage?'disabled':''"
        @click="$emit('update:selected', curPage+1)">
        <icon name="angle-right"></icon>
      </li>
    </ul>
  </nav>
</template>

<script>
/**
 * style: compact|align-right|align-left, 默认非 compact, 居中
 */
import 'components/icons/angle-right'
import 'components/icons/angle-left'
import Icon from 'components/icons/Icon'

export default {
  name: 'pagination',
  data() {
    return {}
  },
  props: {
    totalPage: { type: Number, required: true },
    curPage: { type: Number, required: true },
    size: { type: String, default: '' },
  },
  components: { Icon },
  computed: {
    pageGroups() {
      // 这里的逻辑是选中页加左右紧邻可见的页面个数都尽可能保持 halfSpin 的值
      const halfSpin = this.size === 'small' ? 1 : 2
      const middle = []
      let first = []
      let last = []
      const lSpin = Math.max(halfSpin, halfSpin * 2 - (this.totalPage - this.curPage))
      const rSpin = Math.max(halfSpin, halfSpin * 2 - (this.curPage - 1))
      const midStart = Math.max(1, this.curPage - lSpin)
      const midEnd = Math.min(this.totalPage, this.curPage + rSpin)

      for (let i = midStart; i < midEnd + 1; i++) {
        middle.push(i)
      }
      if (middle.indexOf(1) === -1) {
        if (middle.indexOf(2) !== -1) {
          middle.splice(0, 0, 1)
        } else {
          first = [1]
        }
      }
      if (middle.indexOf(this.totalPage) === -1) {
        if (middle.indexOf(this.totalPage - 1) !== -1) {
          middle.push(this.totalPage)
        } else {
          last = [this.totalPage]
        }
      }
      return { first, middle, last }
    },
  },
}
</script>

<style lang="less" scoped>
  @import "~assets/css/base-variables.less";
  nav {
    font-size: 1rem;
    padding: 2rem 0;
    border: none;
    &.compact {
      padding: 0;
    }
  }

  ul {
    padding: 0;
    text-align: center;
    &.align-right {
      text-align: left;
    }
    &.align-left {
      text-align: right;
    }
  }
  ul li {
    display: inline-block;
    cursor: pointer;
    padding: .2em;
    margin: .2em;
    border-radius: 2px;
    text-align: center;

    span, i {
      min-width: 1.18em;
      display: inline-block;
      line-height: 1.18;
    }

    &.disabled {
      pointer-events: none;
      color: @color-grey-400;
    }
    &:focus, &:hover, &:active {
      background-color: @color-grey-300;
    }
    &.active, &:focus.active, &:hover.active，&:active.active {
      background-color: @color-blue;
      color: @color-white;
      pointer-events: none;
    }
  }
  .small ul li {
    span, i {
      min-width: .8em;
      display: inline-block;
      line-height: 1.18;
    }
  }
</style>



