<template>
  <div class="preview-question preview-objective-blank">
    <div class="q-content">
      <span v-if="index" class="seq-label main-seq-label">{{index}}. </span>
      <span v-if="showDisplayName" class="display-name-label">{{question.displayName}}</span>
      <span v-if="question.score">({{question.score}}分)</span>
      <span v-html="question.stemHtml" class="richtext"></span>
      <div class="sub-questions" v-if="showAnswer">
        <div class="sub-question" v-for="(subQ, index) of question.questions">
          <span class="subq-seqlabel">({{index + 1}})</span>
          <div class="subq-content flex-1">
            <span v-if="showUserAnswer" class="user-answer">
              <span class="text-label text-danger" v-if="!subQ.getUserAnswer(studentId)">未作答</span>
              <template v-else>
                <span :class="subQ.isUserCorrect(studentId)?'user-correct':'user-wrong'">
                  {{subQ.getUserAnswer(studentId)}}
                  <icon :name="subQ.isUserCorrect(studentId)?'check':'remove'"></icon>
                </span>
              </template>
            </span>
            <span class="text-label">正确答案：</span>
            <span class="subq-answer">{{subQ.answerComposed.replace(/\$\$/g, ' 或 ')}}</span>
          </div>
          <div class="q-stat" v-if="showStat && question.hasReport">
            <div class="stat-chart">
              <bar-chart class="normal content"
                :blankQuestion="subQ"
                :totalStuCount="Object.keys(allStudents).length"></bar-chart>
              <div class="overlay content"></div>
              <button class="ui primary button action content" @click="resultModalTarget=subQ">查看详情</button>
            </div>
          </div>
        </div>
      </div>
      <toggle-analysis
        v-if="!answerSheetMode"
        :question="question"
        :unfold="unfoldAnalysis"></toggle-analysis>
    </div>
    <modal-blank-results
      :question="resultModalTarget"
      :allStudents="allStudents"
      v-if="showStat"
      @close="resultModalTarget=null"></modal-blank-results>
  </div>
</template>

<script>
/*
 showAnswer 目前是在预览文件时用到，不涉及到学生作答，暂定 showAnswer > showUserAnswer 和 showStat
 */
import BarChart from 'components/questions/bar-chart'
import ModalBlankResults from 'components/questions/modal-blank-results'
import Icon from 'components/icons/Icon'
import 'components/icons/slim-remove'
import ToggleAnalysis from './toggle-analysis'
import { Q_OBJECTIVE_BLANK } from '../consts'

export default {
  name: 'preview-objective-blank',
  props: {
    question: {
      type: Object,
      required: true,
      validate: value => value.kind === Q_OBJECTIVE_BLANK,
    },
    index: { type: Number, default: 0 },
    showDisplayName: { type: Boolean, default: true },
    unfoldAnalysis: { type: Boolean, default: false },

    showStat: { type: Boolean, required: false, default: false },
    showAnswer: { type: Boolean, default: true },
    showUserAnswer: { type: Boolean, default: false },
    studentId: { type: Number, default: null },
    answerSheetMode: { type: Boolean, default: false },
    allStudents: { type: Object, required: false, default: null },
  },
  data() {
    return {
      resultModalTarget: null,
    }
  },
  components: { ToggleAnalysis, Icon, BarChart, ModalBlankResults },
}
</script>

<style lang="less" scoped>
@color-red: #dd4b39;
@color-teal: #00b5ad;

.user-answer {
  margin-right: 2em;
  font-weight: bold;
  .user-correct {
    color: @color-teal;
  }
  .user-wrong {
    color: @color-red;
  }
}
</style>



