<template>
  <div class="text-editor">
    <div class="editor-wrapper" :style="'height: ' + nRow*1.5 + 'em;'"></div>
    <formula-editor v-if="openFormulaEditor" @close="openFormulaEditor=false"
      @updateFormula="formula => insertOrUpdateFormula(formula)"></formula-editor>
  </div>
</template>

<script>
/* eslint-disable no-undef */
/* 组件 v-model 的实现不完全。props 的传递只有组件初始化的时候起作用。
 * 所以不要用同一个值作为 props 传递个两个 text-editor 组件
 */
import axios from 'axios'
import FormulaEditor from 'components/formula-editor'
import ImageUploadOverlay from 'assets/js/quill/overlay/image-upload'
import { PadDateTime } from 'assets/js/utils'
import Formula from 'assets/js/quill/formula'
import Image from 'assets/js/quill/image'
import BlankPlaceholder from 'assets/js/quill/blank-placeholder'
import { checkRichtextEmpty } from '@/question-bank/richtext'
import { OSS_ENDPOINT, OSS_REGION, OSS_BUCKET } from '@/assets/js/consts/common'

export default {
  name: 'text-editor',
  model: {
    prop: 'content',
    event: 'change',
  },
  props: {
    content: { type: Object, default: () => {} },
    nRow: { type: Number, default: 5 },
    placeholder: { type: String, default: '输入...' },
    required: { type: Boolean, default: true },
    supportBlankBlot: { type: Boolean, default: false },
  },
  data() {
    return {
      quill: null,
      openFormulaEditor: false,
    }
  },
  components: { FormulaEditor },
  mounted() {
    Quill.register('modules/formula', Formula, true)
    Quill.register('formats/image', Image, true)
    Quill.register('formats/blank-placeholder', BlankPlaceholder, true)
    const editor = this.$el.querySelector('.editor-wrapper')
    const Delta = Quill.import('delta')
    const link = Quill.import('formats/link')
    link.sanitize = url => {
      if (!url.startsWith('http'))
        return `http://${url}`
      return url
    }

    this.quill = new Quill(editor, {
      modules: {
        formula: true,
        imageResize: {
          modules: ['Resize', 'DisplaySize'],
        },
        toolbar: {
          container: [
            ['bold', 'italic', 'underline'],
            ['image', 'formula'],
            // ['image', 'link', 'formula'],
          ],
          handlers: {
            image: selectAndUploadImage.bind(this),
            formula: () => {
              this.openFormulaEditor = true
            },
          },
        },
      },
      placeholder: this.placeholder,
      theme: 'pad',
      bounds: editor,
      scrollingContainer: editor,
      formats: ['bold', 'italic', 'underline', 'link', 'image', 'width', 'height', 'formula', 'blank-placeholder'],
      // format 缺少一项 占位符
    })
    function selectAndUploadImage() {
      if (!this.quill) return
      const toolbar = this.quill.getModule('toolbar')
      let fileInput = toolbar.container.querySelector('input.ql-image[type=file]')
      if (fileInput == null) {
        fileInput = document.createElement('input')
        fileInput.setAttribute('type', 'file')
        fileInput.setAttribute('accept', 'image/png, image/gif, image/jpeg, image/bmp')
        fileInput.classList.add('ql-image')
        fileInput.addEventListener('change', () => {
          if (!fileInput.files || !fileInput.files[0]) {
            this.$toasted.error('没有选中文件')
            return
          }
          const file_ = fileInput.files[0]
          const ext = file_.name.split('.').pop().toLowerCase()
          if (['jpg', 'jpeg', 'png', 'bmp'].indexOf(ext) < 0 || !(/^image\//.test(file_.type))) {
            this.$toasted.error('仅支持 jpg/jpeg/png/bmp 格式的图片')
          } else {
            // 1. 以dataURL 形式加入 quill editor, 加[上传中]遮罩
            const reader = new FileReader()
            reader.onload = (e) => {
              const range = this.quill.getSelection(true)
              this.quill.updateContents(new Delta()
                .retain(range.index)
                .delete(range.length)
                .insert({ image: e.target.result }), Quill.sources.USER)
              this.quill.setSelection(range.index + 1, Quill.sources.SILENT)
              fileInput.value = ''
              this.quill.disable()
              const [imageLeaf, offset] = this.quill.getLeaf(range.index + 1)
              imageLeaf.domNode.classList.add('image-uploading')
              const imageOverlay = new ImageUploadOverlay(this.quill, this.quill.container)
              imageOverlay.position(imageLeaf.domNode)
              // 2. 上传文件，遮罩层显示上传进度
              axios.get('/pad/j/misc/aliyun/sts').then(response => {
                if (response.data.status === 1) {
                  this.$toasted.error(response.data.detail + ' 请刷新页面后重试', { duration: 2000, position: 'bottom-center' })
                  return
                }
                const ossBucket = new OSS.Wrapper({
                  region: OSS_REGION,
                  bucket: OSS_BUCKET,
                  ...response.data.sts,
                })
                const padDate = new PadDateTime(new Date())
                const fileKey = `richtext_images/${padDate.formatDate('/')}/${Math.random().toString(36).substr(2, 15)}.${ext}`
                ossBucket.multipartUpload(fileKey, file_, {
                  partSize: 1 * 1024 * 128,
                  *progress(p) {
                    imageOverlay.root.innerHTML = `上传中${parseInt(p * 100)}%...`
                  },
                }).then(response => {
                  if (response.res.status === 200) {
                    // 3. 上传成功，图片恢复为正常状态
                    imageLeaf.domNode.classList.remove('image-uploading')
                    imageOverlay.destroy()
                    imageLeaf.domNode.setAttribute('src', `http://${OSS_BUCKET}.${OSS_ENDPOINT}/${fileKey}`)
                    imageLeaf.domNode.setAttribute('width', imageLeaf.domNode.width)
                    imageLeaf.domNode.setAttribute('height', imageLeaf.domNode.height)
                    this.quill.enable()
                  }
                }).catch(error => this.handleAliOssException(error))
              }).catch(error => this.handleAliOssException(error))
            }
            reader.readAsDataURL(fileInput.files[0])
          }
        })
        toolbar.container.appendChild(fileInput)
      }
      fileInput.click()
    }

    this.quill.clipboard.addMatcher('.ql-blank-placeholder', () => new Delta())
    this.quill.on('text-change', () => {
      this.$nextTick(() => {
        this.$emit('change', this.quill.getContents())
      })
    })
    if (this.supportBlankBlot) {
      this.$eventHub.$on('insert-blank', () => {
        const range = this.quill.getSelection(true)
        const blankNodes = this.$el.querySelectorAll('.ql-blank-placeholder') // 插入新占位符之前原有的占位符数组
        let insertBlotSeq = 1

        for (let i = 1; i <= blankNodes.length; i++) {
          const node = blankNodes[i - 1]
          const nodeBlot = Quill.find(node)

          const blotIndex = this.quill.getIndex(nodeBlot)
          if (insertBlotSeq < i + 1 && blotIndex < range.index) {
            insertBlotSeq = i + 1
          }

          if (blotIndex >= range.index) {
            this.quill.updateContents(
              new Delta().retain(blotIndex).delete(1).insert({ 'blank-placeholder': i + 1 }),
              'silent',
            )
          }
        }
        this.quill.insertEmbed(range.index, 'blank-placeholder', insertBlotSeq, Quill.sources.USER)
        this.quill.setSelection(range.index + 1, Quill.sources.SILENT)
        this.$eventHub.$emit('insert-blank-question', insertBlotSeq)
      })

      this.$eventHub.$on('del-blank', (seq) => {
        // 表达为用户删除了这一特定的 blank-placeholder
        const blankNodes = this.$el.querySelectorAll('.ql-blank-placeholder')
        const nodeBlot = Quill.find(blankNodes[seq - 1])
        const blotIndex = nodeBlot.offset()
        this.quill.updateContents(new Delta().retain(blotIndex).delete(1), 'user')
      })
    }
    this.$eventHub.$on('save-question', () => {
      if (this.$store.state.checkSaving && this.required) {
        if (checkRichtextEmpty(this.content)) {
          this.$store.commit('changeCheckSaving', false)
          this.quill.focus()
        }
      }
    })
    this.quill.on('text-change', (delta, oldContents, source) => {
      let formulaInserted = false
      let blankSyncing = false
      delta.forEach((op) => {
        if (typeof (op.insert) === 'object') {
          if (!formulaInserted && op.insert.hasOwnProperty('formula')) {
            formulaInserted = true
          }
        }
        if (this.supportBlankBlot && op.delete) {
          blankSyncing = true
        }
      })

      if (formulaInserted) {
        this.$nextTick(() => {
          MathJax.Hub.Queue(['Typeset', MathJax.Hub])
        })
      }
      if (blankSyncing) {
        // 空格占位符被删除的情况
        const oldBlankSeqs = oldContents.ops.filter(op =>
          op.insert
          && typeof (op.insert) === 'object'
          && op.insert.hasOwnProperty('blank-placeholder')
        ).map(op => op.insert['blank-placeholder'])

        const newDelta = this.quill.getContents()
        const newBlankSeqs = newDelta.ops.filter(op =>
          op.insert
          && typeof (op.insert) === 'object'
          && op.insert.hasOwnProperty('blank-placeholder')
        ).map(op => op.insert['blank-placeholder'])


        const delSeqs = []
        for (const s of oldBlankSeqs) {
          if (!newBlankSeqs.includes(s)) {
            delSeqs.push(s)
          }
        }

        if (delSeqs.length) {
          // blank-placeholder 序号 reset
          const blankNodes = this.$el.querySelectorAll('.ql-blank-placeholder')
          for (let i = 1; i <= blankNodes.length; i++) {
            const nodeBlot = Quill.find(blankNodes[i - 1])
            if (nodeBlot.domNode.textContent == i)
              continue
            const blotIndex = this.quill.getIndex(nodeBlot)
            this.quill.updateContents(
              new Delta().retain(blotIndex).delete(1).insert({ 'blank-placeholder': i }),
              'silent',
            )
          }

          this.quill.history.clear()
          this.$eventHub.$emit('del-blank-questions', delSeqs)
        }
      }
    })

    this.quill.setContents(this.content)
    window.quill = this.quill
  },
  methods: {
    handleAliOssException(error) {
      if (error.name === 'RequestTimeTooSkewedError') {
        this.$toasted.error('请求发起的时间有错。请确保电脑本地时间处于校准状态。校准后刷新页面重新输入富文本。', {
          duration: 2000,
          position: 'bottom-center',
        })
      } else {
        this.$toasted.error(error, { duration: 2000, position: 'bottom-center' })
      }
    },
    insertOrUpdateFormula(formula) {
      if (!formula) {
        return
      }
      const range = this.quill.getSelection(true)
      if (range != null) {
        const index = range.index + range.length
        this.quill.insertEmbed(index, 'formula', formula, Quill.sources.USER)
        this.quill.insertText(index + 1, ' ', Quill.sources.USER)
        this.quill.setSelection(range.index + 2, Quill.sources.SILENT)
        this.openFormulaEditor = false
      }
    },
  },
}
</script>
<style lang="less">
  @import "~assets/css/quill.less";
  .ql-editor {
    overflow-y: unset;
    .image-uploading {
      opacity: .5;
    }
  }

</style>
<style lang="less" scoped>
  .text-editor {
    position: relative;
  }
  .editor-wrapper {
    // height: 200px; // 必须有 height 值
    box-sizing: content-box;
    min-height: 2em;
    max-height: 400px;
    overflow-y: auto;
  }
  .ql-container {
    font-size: 14px;
  }
</style>



