<template>
  <div class="create-question" id="create-question">
    <dkind-switcher v-if="question"></dkind-switcher>
    <component
      :is="currentView"
      :question="question"
      action="create"
      v-if="selectedDkind && question"
      ></component>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getEditViewByDkind } from '@/question-bank/edit/utils'
import { getModelByDkind } from '@/question-bank/utils'
import { SUBJECT_DISPLAYKIND_MAP } from '@/question-bank/consts'
import DkindSwitcher from './dkind-switcher'

export default {
  name: 'create-question',
  data() {
    const selectedDkind = this.$route.query.display_kind
    const QuestionModel = getModelByDkind(selectedDkind)
    const question = QuestionModel
                     && new QuestionModel({ displayKind: selectedDkind, sourceAction: 'create' })
                     || null
    return { selectedDkind, question }
  },
  created() {
    if (!this.selectedDkind) {
      this.selectedDkind = this.questionDkinds[0].displayKind
    }
  },
  components: { DkindSwitcher },
  computed: {
    currentView() {
      if (!this.selectedDkind) return null
      return getEditViewByDkind(this.selectedDkind)
    },
    ...mapState(['teacher', 'questionDkinds']),
  },
  watch: {
    '$route.query.display_kind': function (val) {
      this.selectedDkind = val
      const QuestionModel = getModelByDkind(val)
      this.question = QuestionModel
                      && new QuestionModel({ displayKind: val, sourceAction: 'create' })
                      || null
    },
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      const validDkinds = SUBJECT_DISPLAYKIND_MAP[vm.$store.state.teacher.subjectId]
      if (!validDkinds.includes(parseInt(to.query.display_kind))) {
        vm.$router.replace({ name: 'create-question', query: { display_kind: validDkinds && validDkinds[0] } })
      }
    })
  },
}
</script>

<style lang="less">
@import "~@/question-bank/edit/index.less";
</style>

<style lang="less" scoped>
.create-question {
  background-color: white;
  padding: 1em;
  position: relative;
}
</style>



