<template>
  <div class="upload-area">
    <button class="ui basic small default button">选择音频文件 *.mp3</button>
    <span v-if="audioFile">{{audioFile.name}}</span>
    <span v-else-if="audioPathWrite">{{audioPathWrite}}</span>
    <label for="file-selector"></label>
    <input type="file" id="file-selector" accept=".mp3">
    <modal v-if="showProgressBar" :withHeader="false" @close="showProgressBar=false">
      <div slot="body">
        <progress-bar
          class="info"
          :percent="percent"
          :showPercent="true"
          :label="progressLabel"></progress-bar>
      </div>
    </modal>
  </div>
</template>


<script>
import axios from 'axios'
import Icon from 'components/icons/Icon'
import 'components/icons/slim-remove'
import Modal from 'components/modal'
import ProgressBar from 'components/progress-bar'
import { PadDateTime } from 'assets/js/utils'
import { OSS_REGION, OSS_BUCKET } from '@/assets/js/consts/common'
import AudioUploader from './audio-uploader'

export default {
  name: 'audio-uploader',
  props: {
    audioPathWrite: { type: String },
  },
  data() {
    return {
      audioFile: null,
      percent: 0,
      progressLabel: '',
      showProgressBar: false,
    }
  },
  components: { Icon, AudioUploader, Modal, ProgressBar },
  mounted() {
    const fileInput = document.getElementById('file-selector')
    fileInput.addEventListener('change', () => {
      const file_ = fileInput.files[0]
      const ext = file_.name.split('.').pop().toLowerCase()
      if (ext !== 'mp3') {
        this.$toasted.error('音频文件格式不支持，请选择 mp3 文件', { duration: 1000, position: 'top-center' })
        return
      }
      this.audioFile = file_
    })
  },
  methods: {
    handleAliOssException(error) {
      if (error.name === 'RequestTimeTooSkewedError') {
        this.$toasted.error('请求发起的时间有错。请确保电脑本地时间处于校准状态。', {
          duration: 2000,
          position: 'bottom-center',
        })
      } else {
        this.$toasted.error(error, { duration: 2000, position: 'bottom-center' })
      }
      this.showProgressBar = false
      this.$nextTick(() => {
        this.percent = 0
        this.progressLabel = ''
      })
    },
    startUploading() {
      if (!this.audioFile) {
        this.$toasted.error('请选择文件')
        return
      }
      axios.get('/pad/j/misc/aliyun/sts').then(response => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 2000, position: 'bottom-center' })
          return
        }

        this.showProgressBar = true
        this.progressLabel = '正在上传音频文件，请不要关闭本页'
        const ossBucket = new OSS.Wrapper({
          region: OSS_REGION,
          bucket: OSS_BUCKET,
          ...response.data.sts,
        })
        const ext = this.audioFile.name.split('.').pop().toLowerCase()
        const padDate = new PadDateTime(new Date())
        const fileKey = `listen_audio/${padDate.formatDate('/')}/${Math.random().toString(36).substr(2, 15)}.${ext}`
        const that = this
        ossBucket.multipartUpload(fileKey, this.audioFile, {
          partSize: 1 * 1024 * 128,
          *progress(p) {
            that.percent = parseInt(p * 100)
          },
        }).then(response => {
          if (response.res.status === 200) {
            this.progressLabel = '上传成功'
            this.$emit('uploadSucceed', fileKey)
            this.audioFile = null
          } else {
            this.$toasted.error('上传文件失败')
          }
          this.showProgressBar = false
          this.$nextTick(() => {
            this.percent = 0
            this.progressLabel = ''
          })
        }).catch(error => this.handleAliOssException(error))
      }).catch(error => this.handleAliOssException(error))
    },
  },
}
</script>

<style lang="less" scoped>
.upload-area {
  margin-top: -.5em;
  position: relative;
  label {
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  input[type=file] {
    opacity: 0;
    z-index: -1;
  }
}
</style>



