import { difference } from 'lodash'
import { Q_DISPLAY_KIND_MAP, STATE_LABEL_MAP, Q_ESSAY } from './consts'
import {
  parsedRtToText,
  parseRichProperties,
  replacer,
} from './richtext'


/*
 * sourceData.sourceAction: 会影响实例的构造和方法
 *   `preview`
 *   `create`: init an empty question
 *   `edit`
 *   `duplicate`
 */
export class BaseQuestion {
  constructor(sourceData, questionKind) {
    if (sourceData.hasOwnProperty('kind') && sourceData.kind !== questionKind) {
      throw '题目 kind 值错误'
    }
    this.kind = questionKind
    this.isSubjective = questionKind === Q_ESSAY
    this.sourceAction = sourceData.sourceAction || ''
    this.uuid = sourceData.sourceAction === 'duplicate' ? null : sourceData.uuid
    this.score = sourceData.score || null

    const displayKind = sourceData.displayKind
    if (!displayKind || !Q_DISPLAY_KIND_MAP.hasOwnProperty(displayKind)) {
      throw '题目 displayKind 值错误'
    }
    this.displayKind = displayKind
    this.displayName = Q_DISPLAY_KIND_MAP[this.displayKind].displayName
    this.subjectId = Q_DISPLAY_KIND_MAP[this.displayKind].subjectId

    this.versionId = sourceData.versionId || 0
    this.authorId = sourceData.authorId || 0
    this.difficulty = sourceData.difficulty || 0
    this.chapters = sourceData.chapters || []
    this.knowledges = sourceData.knowledges || []
    this.source = sourceData.source || ''
    this.state = sourceData.sourceAction === 'duplicate' ? 3000 : sourceData.state || 3000

    this.increKey = sourceData.increKey || 1

    this.hasReport = false
    this.analysisFolded = true
  }

  get onPreview() {
    return this.sourceAction === 'preview'
  }

  get onEdit() {
    return ['create', 'edit', 'duplicate'].includes(this.sourceAction)
  }

  get stateLabel() {
    return STATE_LABEL_MAP[this.state]
  }

  get abstract() {
    let text = ''
    if (this.stemRich) {
      text = parsedRtToText(this.stemRich)
    }
    if (!text && this.options) {
      for (const option of this.options) {
        text += option.abstract
      }
    }
    if (text.length > 100) {
      return `${text.slice(0, 100)}...`
    }
    return text || '无摘要'
  }

  commonInfoValidate() {
    if (!this.kind) return [false, '缺少 kind 信息']
    if (!this.displayKind) return [false, '缺少 displayKind 信息']
    if (!this.displayName) return [false, '缺少 displayName 信息']
    if (!this.difficulty) return [false, '请设置难度']
    if (this.chapters.length === 0) return [false, '请关联章节']
    if (this.knowledges.length === 0) return [false, '请关联知识点']
  }

  validate() {
    // error = [needPostioned, msg]
    const commonError = this.commonInfoValidate()
    if (commonError) return commonError

    if (this.hasOwnProperty('stemRich') && this.stemEmpty) {
      return [true, '题干不能为空']
    }
    if (this.hasOwnProperty('answerRich') && this.answerEmpty) {
      return [true, '答案不能为空']
    }
    if (this.hasOwnProperty('options')) {
      const len = this.options.length
      if (len < 2) {
        return [false, '选择题/多选题请至少保留两个选项']
      }
      if (this.hasOwnProperty('answerSeq')) {
        if (this.answerSeq < 0 || this.answerSeq >= len) {
          return [false, '答案设置有问题']
        }
      }
      let checkedOption = null
      for (const option of this.options) {
        const error = option.validate()
        if (error) return error
        if (option.isAnswer) {
          checkedOption = option
        }
      }
      if (!checkedOption) {
        return [false, '请勾选一个答案']
      }
    }
    if (this.hasOwnProperty('questions')) {
      if (this.questions.length <= 0) {
        return [false, '至少要有一个小题']
      }
      for (const subQ of this.questions) {
        const error = subQ.validate()
        if (error) return error
      }
    }
  }

  stringify() {
    return JSON.stringify(this, replacer)
  }

  windup() {
    Object.defineProperty(
      this, 'subQuestionCount', { get() { return 'questions' in this ? this.questions.length : 1 } })
    parseRichProperties(this)
  }
}


export class Option {
  constructor(sourceData = {}) {
    this.id = sourceData.sourceAction === 'duplicate' ? null : sourceData.id
    this.seq = sourceData.seq || 0
    this.isAnswer = sourceData.isAnswer || false
    this.textRich = sourceData.textRich || {}
    this.vkey = sourceData.vkey || 1
    this.sids = []
    this.multiAnswer = sourceData || false

    this.hasReport = false

    Object.defineProperty(
      this, 'seqLabel', { get() { return String.fromCharCode(65 + this.seq) } })
    parseRichProperties(this)
  }

  get abstract() {
    return `${this.seqLabel}. ${parsedRtToText(this.textRich)}`
  }

  validate() {
    if (this.textEmpty) {
      return [true, '请输入选项文本']
    }
  }

  isUserAnswer(studentId) {
    return this.sids.includes(studentId)
  }

  injectReportData(data) {
    this.sids = data[this.seq] || []
    this.hasReport = true
  }
}


export class BaseSubQuestion {
  constructor(sourceData) {
    this.sourceAction = sourceData.sourceAction || ''
    this.id = sourceData.sourceAction === 'duplicate' ? null : sourceData.id
    this.seq = sourceData.seq || 0
    this.vkey = sourceData.vkey || 1
    this.increKey = this.vkey * 1000

    this.hasReport = false
  }

  get onPreview() {
    return this.sourceAction === 'preview'
  }

  get onEdit() {
    return ['create', 'edit', 'duplicate'].includes(this.sourceAction)
  }

  validate() {
    // error = [needPostioned, msg]

    if (this.hasOwnProperty('textRich') && this.textEmpty) {
      return [true, '请输入内容']
    }
    if (this.hasOwnProperty('answer') && this.answer.length <= 0) {
      return [false, '答案必填']
    }

    if (this.hasOwnProperty('options')) {
      const len = this.options.length
      if (len < 2) {
        return [false, '选择题/多选题请至少保留两个选项']
      }
      if (this.hasOwnProperty('answerSeq')) {
        if (this.answerSeq < 0 || this.answerSeq >= len) {
          return [false, '答案设置有问题']
        }
      }
      let checkedOption = null
      for (const option of this.options) {
        const error = option.validate()
        if (error) return error
        if (option.isAnswer) {
          checkedOption = option
        }
      }
      if (!checkedOption) {
        return [false, '请勾选一个答案']
      }
    }
  }

  richtextPreparation() {
    parseRichProperties(this)
  }
}


export const ChoiceReportMixin = {
  injectReportData(data, allSids) {
    let answeredSids
    if (this.structureLevel === 1) {
      answeredSids = []
      for (const option of this.options) {
        option.injectReportData(data)
        answeredSids = answeredSids.concat(option.sids)
      }
      this.unansweredSids = difference(allSids, answeredSids)
    } else if (this.structureLevel === 2) {
      for (const subq of this.questions) {
        const subqReportData = data[subq.id] || {}
        answeredSids = []
        for (const option of subq.options) {
          option.injectReportData(subqReportData)
          answeredSids = answeredSids.concat(option.sids)
        }
        subq.unansweredSids = difference(allSids, answeredSids)
        subq.hasReport = true
      }
    }
    this.hasReport = true
  },
}



