<template>
  <div class="preview-question preview-essay">
    <div class="q-content">
      <span v-if="index" class="seq-label main-seq-label">{{index}}. </span>
      <span v-if="showDisplayName" class="display-name-label">{{question.displayName}}</span>
      <span v-if="question.score">({{question.score}}分)</span>
      <span v-html="question.stemHtml" class="richtext"></span>
      <subjective-result v-if="showUserAnswer"
        :question="question"
        :studentId="studentId"
        :student="allStudents[studentId]"></subjective-result>
      <toggle-analysis :customSlot="true" v-if="showStat">
        <span slot="toggleTitle">作答详情</span>
        <span slot="subTitle" v-if="question.hasReport && totalStuCount">{{question.results.length}}人已作答, {{totalStuCount - question.results.length}}人未作答</span>
        <template slot="toggleContent">
          <inline-subjective-results
            :question="question"
            :allStudents="allStudents"></inline-subjective-results>
        </template>
      </toggle-analysis>
      <toggle-analysis
        v-if="!answerSheetMode"
        :question="question"
        :unfold="unfoldAnalysis"></toggle-analysis>
    </div>
  </div>
</template>

<script>
import InlineSubjectiveResults from 'components/questions/inline-subjective-results'
import SubjectiveResult from 'components/questions/subjective-result'
import Icon from 'components/icons/Icon'
import 'components/icons/slim-remove'
import { Q_ESSAY } from '../consts'
import ToggleAnalysis from './toggle-analysis'

export default {
  name: 'preview-essay',
  props: {
    question: {
      type: Object,
      required: true,
      validate: value => value.kind === Q_ESSAY,
    },
    index: { type: Number, default: 0 },
    showDisplayName: { type: Boolean, default: true },
    unfoldAnalysis: { type: Boolean, default: false },

    showStat: { type: Boolean, required: false, default: false },
    showAnswer: { type: Boolean, default: true },
    showUserAnswer: { type: Boolean, default: false },
    studentId: { type: Number, default: null },
    answerSheetMode: { type: Boolean, default: false },
    allStudents: { type: Object, required: false, default: null },
  },
  data() {
    return {}
  },
  components: { Icon, ToggleAnalysis, InlineSubjectiveResults, SubjectiveResult },
  mounted() {},
  computed: {
    totalStuCount() {
      if (!this.allStudents) return 0
      return Object.keys(this.allStudents).length
    },
  },
}
</script>

<style lang="less" scoped>
</style>



