/* eslint-disable no-undef */
const Embed = Quill.import('blots/embed')


class BlankPlaceholder extends Embed {
  static create(value) {
    const node = super.create(value)
    node.setAttribute('data-seq', value)

    const labelSpan = document.createElement('span')
    labelSpan.textContent = value
    labelSpan.classList.add('blank-placeholder-label')
    node.appendChild(labelSpan)

    return node
  }

  static value(domNode) {
    return parseInt(domNode.getAttribute('data-seq'))
  }
}

BlankPlaceholder.blotName = 'blank-placeholder'
BlankPlaceholder.className = 'ql-blank-placeholder'
BlankPlaceholder.tagName = 'SPAN'

export default BlankPlaceholder



