<template>
  <nav class="secondary-navbar">
    <div class="ui compact secondary pointing blue menu">
      <!-- <router-link :to="{name: 'board-list'}" tag="a" class="item">我收藏的</router-link> -->
      <router-link
        tag='div'
        :to="{name: 'bank-gallery', query: {tree_type: 'knowledge'}}"
        class="dropdown item"
        :class="$route.name==='bank-gallery'?'active':''">
        <div class="text">手动组卷</div>
        <i class="dropdown icon"></i>
        <div class="menu">
          <router-link :to="{name: 'bank-gallery', query: {tree_type: 'chapter'}}" tag="a" class="menu-item" exact>同步章节组卷</router-link>
          <router-link :to="{name: 'bank-gallery', query: {tree_type: 'knowledge'}}" tag="a" class="menu-item" exact>知识点组卷</router-link>
        </div>
      </router-link>
      <router-link :to="{name: 'manage-questions'}" tag="a" class="item">管理题目</router-link>
      <router-link
        v-if="initCreateDKind"
        :to="{name: 'create-question', query: {display_kind: initCreateDKind}}"
        tag="a"
        class="item"
        ><i class="add square icon"></i>新建题目</router-link>
    </div>
  </nav>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'secondary-navbar',
  data() {
    return {}
  },
  computed: {
    initCreateDKind() {
      return this.questionDkinds.length && this.questionDkinds[0].displayKind || null
    },
    ...mapState(['questionDkinds']),
  },
}
</script>

<style lang="less" scoped>
@color-teal: #00b5ad;
@color-blue: #42A5F5;

.dropdown.item {
  position: relative;
  .menu {
    display: none;
    position: absolute;
    top: 95%;
    left: 0;
    width: max-content;
    text-align: left;
    border: 1px solid #22242626;
    transition: all .3s linear;
    z-index: 2;
    .menu-item {
      cursor: pointer;
      display: block;
      padding: .5em;
      line-height: 1.8;
      color: #666;
      font-weight: normal;
      text-decoration: none;
      &.active {
        color: #444;
        font-weight: bold;
      }
      &:hover, &:focus {
        background-color: #f6f6f6;
      }
    }
  }

  &:hover {
    .menu {
      display: block;
    }
  }
}

.secondary-navbar {
  text-align: center;
  background-color: white;
  border-bottom: 1px solid #e0e0e0;
  position: relative;
  .ui.secondary.pointing.menu {
    margin: 0 auto;
    border-bottom-color: transparent;
    height: 40px;
    font-size: 13px;
    .item {
      color: #888;
      .add.icon {
        color: @color-teal;
      }
    }
    .item:last-child {
      border-radius: 0;
    }
  }
}


.ui.secondary.pointing.menu .ui.dropdown .menu .active.item {
  border-color: transparent !important;
}
</style>



