import Vue from 'vue'
import Vuex from 'vuex'
import Toasted from 'vue-toasted'
import VueRouter from 'vue-router'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'
import Navbar from 'components/navbar'
import Course from 'assets/js/course'
import { NEW_BANK_ACCESSABLE_SUBJECT_IDS } from '@/assets/js/consts/common'
import { getQuestionDkindBySubjectId } from '@/question-bank/consts'
import SecondaryNavbar from './secondary-navbar'
import BankGallery from './bank-gallery'
import CreateQuestion from './create-question'
import EditQuestion from './edit-question'
import DuplicateQuestions from './duplicate-question'
import ManageQuestions from './manage-questions'

require('./index.less')

Vue.config.productionTip = false

const axios = require('axios')

axios.defaults.headers.common.X_REQUESTED_WITH = 'XMLHttpRequest'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'
axios.defaults.xsrfCookieName = 'csrftoken'

Vue.prototype.$raven = Raven
if (process.env.NODE_ENV === 'production') {
  Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
}

Vue.use(Vuex)
Vue.use(VueRouter)
Vue.use(Toasted, { duration: 1000, position: 'bottom-center' })

const store = new Vuex.Store({
  state: {
    teacher: null,
    courses: [],
    checkSaving: true,
    questionDkinds: [],
  },
  mutations: {
    updateTeacher(state, payload) {
      state.teacher = payload.teacher
      state.courses = []
      for (const courseData of payload.teacher.courses) {
        state.courses.push(new Course(courseData))
      }
      state.questionDkinds = getQuestionDkindBySubjectId(payload.teacher.subjectId)
    },
    changeCheckSaving(state, flag) {
      state.checkSaving = flag
    },
  },
  getters: {
    school: state => (state.teacher ? state.teacher.school : null),
    classes: state => (state.teacher ? state.teacher.classes : []),
  },
})

const router = new VueRouter({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/pad/question-bank' : '/static/w/pages/question-bank.html',
  routes: [
    // { path: '', redirect: { name: 'bank-gallery', query: { tree_type: 'chapter' } } },
    { path: '', redirect: { name: 'bank-gallery', query: { tree_type: 'knowledge' } } },
    { path: '/gallery', component: BankGallery, name: 'bank-gallery' },
    { path: '/edit/:uuid', component: EditQuestion, name: 'edit-question' },
    { path: '/duplicate/:uuid', component: DuplicateQuestions, name: 'duplicate-question' },
    { path: '/create', component: CreateQuestion, name: 'create-question' },
    { path: '/manage', component: ManageQuestions, name: 'manage-questions' },
  ],
  linkActiveClass: 'active',
})


router.beforeResolve((to, from, next) => {
  if (!NEW_BANK_ACCESSABLE_SUBJECT_IDS.includes(store.state.teacher.subjectId)) {
    window.alert('你所在的学科暂未开放新题库功能')
    window.location.href = '/pad/resource'
  } else {
    next()
  }
})

router.beforeEach((to, from, next) => {
  if (!store.state.teacher) {
    axios.get('/pad/j/teacher_info').then((response) => {
      const teacher = response.data.teacher
      store.commit('updateTeacher', { teacher })
      if (process.env.NODE_ENV === 'production') {
        Raven.setUserContext({
          username: teacher.name,
          id: teacher.id,
        })
      }
      next()
    })
  } else {
    next()
  }
})

Vue.prototype.$eventHub = new Vue()

; (async () => {
  process.env.NODE_ENV === 'development' && await import(/* webpackChunkName: "mock" */ '@/mock')
  new Vue({
    delimiters: ['${', '}'],
    el: '#app',
    store,
    router,
    data: {},
    components: { Navbar, SecondaryNavbar },
    created() {
    },
  })
})()



