import SingleChoiceView from '@/question-bank/edit/single-choice'
import MultiChoiceView from '@/question-bank/edit/multi-choice'
import ReadingChoiceView from '@/question-bank/edit/reading-choice'
import ClozeView from '@/question-bank/edit/cloze'
import ShortDialogView from '@/question-bank/edit/short-dialog'
import LongDialogView from '@/question-bank/edit/long-dialog'
import ObjectiveBlankView from '@/question-bank/edit/objective-blank'
import EssayView from '@/question-bank/edit/essay'
import TrueFalseView from '@/question-bank/edit/true-false'
import MatchingView from '@/question-bank/edit/matching'

import {
  Q_DISPLAY_KIND_MAP,
  Q_SINGLE_CHOICE, Q_TRUE_FALSE, Q_MULTI_CHOICE, Q_OBJECTIVE_BLANK,
  Q_ESSAY, Q_READING_CHOICE, Q_CLOZE, Q_MATCHING, Q_SHORT_DIALOG, Q_LONG_DIALOG,
} from '@/question-bank/consts'


export const KIND_TO_MODEL = {
  [Q_SINGLE_CHOICE]: SingleChoiceView,
  [Q_MULTI_CHOICE]: MultiChoiceView,
  [Q_READING_CHOICE]: ReadingChoiceView,
  [Q_OBJECTIVE_BLANK]: ObjectiveBlankView,
  [Q_CLOZE]: ClozeView,
  [Q_SHORT_DIALOG]: ShortDialogView,
  [Q_LONG_DIALOG]: LongDialogView,
  [Q_ESSAY]: EssayView,
  [Q_TRUE_FALSE]: TrueFalseView,
  [Q_MATCHING]: MatchingView,
}

export function getEditViewByDkind(displayKind) {
  const kind = Q_DISPLAY_KIND_MAP[displayKind].kind
  return KIND_TO_MODEL[kind]
}



