<template>
  <page-modal
    v-if="open"
    @close="$emit('close')"
    class="modal-small knowledge-tag-modal">
    <template slot="content">
      <h4 class="ui header">选择知识点</h4>
      <knowledge-selector
        key="sidebar-selector"
        :mustSelectOne="false"
        :initFirstSelected="false"
        :multiSelect="true"
        :onlyLeafCanSelect="false"
        :openLevel="2"
        :selectedNodes="selectedKnowledges"
      ></knowledge-selector>
      <div class="collected-nodes">
        已选知识点：{{selectedKnowledges.length === 0 ? '无':''}}
        <span v-for="k of selectedKnowledges" class="ui knowledge label">
          {{k.label}}
          <span class="action-icon" @click="removeKnowledge(k)"><icon name="slim-remove"></icon></span>
        </span>
      </div>
    </template>
  </page-modal>
</template>

<script>
import PageModal from 'components/page-modal'
import KnowledgeSelector from 'components/knowledge-selector'
import Icon from 'components/icons/Icon'
import 'components/icons/slim-remove'

export default {
  name: 'knowledge-tag-modal',
  props: {
    selectedKnowledges: { type: Array, default: () => [] },
    open: { type: Boolean, default: false },
  },
  data() {
    return {
    }
  },
  components: { PageModal, KnowledgeSelector, Icon },
  computed: {},
  methods: {
    removeKnowledge(knowledge) {
      const index = this.selectedKnowledges.indexOf(knowledge)
      if (index >= 0) {
        this.selectedKnowledges.splice(index, 1)
      }
    },
  },
}
</script>

<style lang="less">
.knowledge-tag-modal.page-modal .modal-container {
  border-radius: 0;
  background-color: white;

  max-height: unset;
  padding: 0 !important;
  overflow-y: visible;

  .ui.header {
    margin: .5em 0;
    padding: 0 .5em;
  }

  .knowledge-selector {
    margin-top: -1px;
    font-size: 12px;
    overflow-y: auto;
    border: none;
    background-color: #f9f9f9;
    box-shadow: inset 0 0 -1px rgba(0,0,0,.5);

    .ui.basic.button {
      border: none;
    }

    .book-tree {
      max-height: 60vh;
    }
  }
  .collected-nodes {
    padding: 1em .5em;
    min-height: 5em;
    border-top: solid 1px #ddd;
  }
}

</style>



