<template>
  <div class="manage-question" id="manage-question">
    <div class="sidebar-tree">
      <div>
        <div class="sidebar-title">
          <span>根据章节筛选</span>
          <span class="link search link-primary" v-if="!searchMode" @click="searchMode=true"><i class="search icon"></i></span>
          <span class="link link-primary" v-if="searchMode" @click="searchMode=false">返回章节树</span>
        </div>
        <v-select
          :on-search="searchChapter"
          :options="options"
          :on-change="searchChapterIdChanged"
          placeholder="输入关键词，搜索所有教材的章节"
          v-show="searchMode">
        </v-select>
        <chapter-selector
          bookSource="question"
          :onlyLeafCanSelect="false"
          @selectChapter="chapterId=>selectedChapterId=chapterId"
          key="question-chapter-selector"
          v-show="!searchMode"
          class="question-chapter-selector"></chapter-selector>
      </div>
    </div>
    <div class="question-container" id="question-container">
      <div class="filter-section">
        <div v-if="teacher" class="pill-filter">
          题型：
          <span key="displaykind-0" @click="displayKind=0" class="filter-label" :class="displayKind===0?'active':''">全部</span>
          <span
            v-for="dKind of questionDkinds"
            :key="'displaykind-' + dKind.displayKind"
            @click="displayKind=dKind.displayKind"
            class="filter-label"
            :class="displayKind===dKind.displayKind?'active':''">{{dKind.displayName}}</span>
        </div>
        <div class="pill-filter">
          难度：
          <span @click="difficulty=0" class="filter-label" :class="difficulty===0?'active':''">全部</span>
          <span v-for="d of difficulties" @click="difficulty=d.value" class="filter-label"
           :class="d.value===difficulty?'active':''" v-bind:key="d.value">{{d.label}}</span>
        </div>
        <div class="pill-filter">
          状态：
          <span @click="state=0" class="filter-label" :class="state===0?'active':''">全部</span>
          <span v-for="s of states" @click="state=s.value" class="filter-label"
           :class="s.value===state?'active':''" v-bind:key="s.value">{{s.label}}</span>
        </div>
      </div>
      <div class="question-list">
        <loader v-if="loading"></loader>
        <table class="ui very celled table" v-if="questions.length">
          <thead>
            <tr>
              <th class="mini">序号</th>
              <th class="tiny">题型</th>
              <th class="tiny">状态</th>
              <th>摘要（点击预览）</th>
              <th class="row-actions">操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(question, index) of questions" :class="'state-'+question.state" v-bind:key="question.uuid">
              <td class="mini">{{index+1}}</td>
              <td class="tiny">{{question.displayName}}</td>
              <td class="tiny">{{question.stateLabel}}</td>
              <td @click="questionToPreview=question" class="question-abstract">{{question.abstract}}</td>
              <td class="row-actions">
                <span
                  class="link link-primary"
                  v-if="canEdit(question)"
                  @click="openEditQuestion(question)">编辑</span>
                <span
                  class="link link-primary"
                  v-if="canPublish(question)"
                  @click="questionToPublish=question">发布</span>
                <span
                  class="link link-danger"
                  v-if="canDisable(question)"
                  @click="questionToDisable=question">停用</span>
                <span
                  class="link link-danger"
                  v-if="canDiscard(question)"
                  @click="questionToDiscard=question">删除</span>
                <span
                  class="link link-primary"
                  @click="openDuplicateQuestion(question)">复制</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="content-empty" v-if="!loading && questions.length===0">
        <img :src="require('assets/img/content-empty.png')">
        <p>
          什么也没有，试试<span class="text-danger">减少筛选条件</span>或者创建新题。
        </p>
      </div>
      <pagination class="pagination"
        :totalPage="totalPage" :curPage="selectedPage"
        @update:selected="page=>fetchQuestions(page)" v-show="!loading && questions.length"></pagination>
    </div>
    <single-preview-modal
      v-if="questionToPreview"
      @close="questionToPreview=null"
      :question="questionToPreview">
    </single-preview-modal>
    <modal v-if="questionToPublish" :withHeader="false" @close="questionToPublish=null">
      <div slot="body">
        <h3><i class="exclamation circle icon"></i> 确定要发布本题吗？</h3>
        <p>发布后可以停用/删除/复制此题, 但不能在原题上进行编辑修改。</p>
      </div>
      <div slot="footer">
        <button class="ui basic button" @click="questionToPublish=null">取消</button>
        <button class="ui primary button" @click="publish(questionToPublish)">发布</button>
      </div>
    </modal>
    <modal v-if="questionToDisable" :withHeader="false" @close="questionToDisable=null">
      <div slot="body">
        <h3><i class="exclamation circle icon"></i> 确定要停用本题吗？</h3>
        <p>停用后此题将不再出现在组卷页面。你之后可以再发布此题，但已不能在原题上进行编辑修改。</p>
      </div>
      <div slot="footer">
        <button class="ui basic button" @click="questionToDisable=null">取消</button>
        <button class="ui red button" @click="disable(questionToDisable)">停用</button>
      </div>
    </modal>
    <modal v-if="questionToDiscard" :withHeader="false" @close="questionToDiscard=null">
      <div slot="body">
        <h3><i class="exclamation circle icon"></i> 确定要删除本题吗？</h3>
        <p>删除后将不能恢复此题。</p>
      </div>
      <div slot="footer">
        <button class="ui basic button" @click="questionToDiscard=null">取消</button>
        <button class="ui red button" @click="discard(questionToDiscard)">删除</button>
      </div>
    </modal>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import _ from 'lodash'
import Modal from 'components/modal'
import Pagination from 'components/pagination'
import ChapterSelector from 'components/chapter-selector'
import SinglePreviewModal from '@/question-bank/preview/single-preview-modal'
import {
  DIFFICULTIES,
  STATE_EDITING,
  STATE_APPROVED,
  STATE_DISCARDED,
  STATE_DISABLED,
  STATES,
} from '@/question-bank/consts'
import Loader from 'components/loader'
import { getModelByDkind } from '@/question-bank/utils'

const qs = require('qs')

export default {
  name: 'manage-question',
  props: {},
  data() {
    return {
      loading: false,
      questionSource: 'bank',
      questions: [],
      totalPage: 0,
      selectedPage: 1,
      searchName: '',
      options: [],
      searchMode: false,
      selectedChapterId: 0,

      difficulty: 0,
      difficulties: DIFFICULTIES,
      state: 0,
      states: STATES,
      displayKind: 0,

      questionToPreview: null,
      questionToPublish: null,
      questionToDisable: null,
      questionToDiscard: null,
    }
  },
  components: {
    Pagination,
    ChapterSelector,
    Loader,
    Modal,
    SinglePreviewModal,
    vSelect: () => import(/* webpackChunkName: "vue-select" */ 'vue-select'),
  },
  mounted() {
    $(window).scroll(() => {
      const windowTop = $(window).scrollTop()
      if (windowTop >= 118) {
        $('.sidebar-tree').addClass('fixed')
      }
      if (windowTop < 118) {
        $('.sidebar-tree').removeClass('fixed')
      }
    })
    this.$nextTick(() => this.fetchQuestions())
  },
  watch: {
    selectedChapterId() {
      if (this.questionSource === 'bank') { this.fetchQuestions() }
    },
    questionSource() { this.fetchQuestions() },
    difficulty() { this.fetchQuestions() },
    state() { this.fetchQuestions() },
    displayKind() { this.fetchQuestions() },
    loading(val, oldVal) {
      if (val && !oldVal) {
        const windowTop = $(window).scrollTop()
        if (windowTop >= 118) {
          $(window).scrollTop(118)
        }
      }
    },
  },
  computed: {
    ...mapState(['teacher', 'questionDkinds']),
  },
  methods: {
    openDuplicateQuestion(question) {
      const routeData = this.$router.resolve({
        name: 'duplicate-question',
        params: { uuid: question.uuid },
      })
      window.open(routeData.href, '_blank');
    },
    openEditQuestion(question) {
      const routeData = this.$router.resolve({
        name: 'edit-question',
        params: { uuid: question.uuid },
      })
      window.open(routeData.href, '_blank');
    },
    udpdateQuestion(question, action, toState) {
      const params = {
        action,
        uuid: question.uuid,
      }
      return axios.post('/question/j/questions/update', qs.stringify(params)).then((response) => {
        if (response.data.status === 0) {
          this.$toasted.info('操作成功', {
            duration: 500,
            position: 'bottom-center',
            onComplete: () => {
              question.state = toState
              if (this.questionToPublish) this.questionToPublish = null
              if (this.questionToDisable) this.questionToDisable = null
              if (this.questionToDiscard) this.questionToDiscard = null
            },
          })
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail)
        }
      })
    },
    publish(question) {
      if (question && this.canPublish(question)) {
        this.udpdateQuestion(question, 'publish', STATE_APPROVED)
      }
    },
    disable(question) {
      if (question && this.canDisable(question)) {
        this.udpdateQuestion(question, 'disable', STATE_DISABLED)
      }
    },
    discard(question) {
      if (question && this.canDiscard(question)) {
        this.udpdateQuestion(question, 'discard', STATE_DISCARDED)
      }
    },
    canEdit(question) {
      return question.authorId === this.teacher.id && question.state === STATE_EDITING
    },
    canPublish(question) {
      return question.authorId === this.teacher.id
             && [STATE_EDITING, STATE_DISABLED].includes(question.state)
    },
    canDisable(question) {
      return question.authorId === this.teacher.id && question.state === STATE_APPROVED
    },
    canDiscard(question) {
      return question.authorId === this.teacher.id && question.state !== STATE_DISCARDED
    },
    searchChapterIdChanged(val) {
      if (val) {
        this.selectedChapterId = val.value
      }
    },
    searchChapter(search, loading) {
      loading(true)
      this.getsearchedChpater(search, loading, this)
    },
    getsearchedChpater: _.debounce((search, loading, vm) => {
      axios.get('/pad/j/resource/chapters/search', { params: { name: search, source: 3 } })
        .then(response => {
          if (response.data.status === 0) {
            vm.options = response.data.chapters
            loading(false)
          } else if (response.data.status === 1) {
            this.$toasted.error(response.data.detail)
            loading(false)
          }
        })
    }, 250),
    fetchQuestions(page = 1) {
      this.loading = true
      const url = '/question/j/questions/mine'
      const params = {
        page,
        pageSize: 15,
        chapterId: this.selectedChapterId,
        displayKind: this.displayKind,
        difficulty: this.difficulty,
        state: this.state,
      }
      return axios.get(url, { params }).then(response => {
        if (response.data.status === 0) {
          this.questions = []
          for (const questionData of response.data.questions) {
            const QuestionModel = getModelByDkind(questionData.displayKind)
            this.questions.push(new QuestionModel(questionData))
          }
          this.totalPage = response.data.totalPage
          this.selectedPage = page
          this.loading = false
          this.$nextTick().then(() => {
            MathJax.Hub.Queue(['Typeset', MathJax.Hub])
          })
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail)
          this.loading = false
        }
      })
    },
    selectNodes(nodes) {
      if (nodes.length) {
        this.selectedChapterId = nodes[0].id
      } else {
        this.selectedChapterId = 0
      }
    },
  },
}
</script>

<style lang="less">
@import "~@/question-bank/preview/index.less";
input.set-score {
  width: 3.5em;
  margin: -.5em .2em;
  line-height: 1.5;
}
.v-select.loading {
  font-size: 13px;
  margin-top: .6rem;
  .spinner, .spinner:after {
    top: .8em;
    width: 1.5em;
    height: 1.5em;
  }
}
</style>

<style lang="less" scoped>
@color-blue: #42A5F5;
@color-red: #dd4b39;
@color-teal: #00b5ad;

.ui.celled.table {
  border-left: none;
  border-right: none;
  border-radius: 0;
}
.container-tab-menu.menu {
  margin-bottom: 2rem;
}
.link.search.link-primary {
  line-height: 1.3;
  font-size: 1.2em;
  float: right;
  i.icon {
    margin-right: 0;
  }
}

.manage-question {
  .sidebar-tree, .question-container {
    display: inline-block;
    background-color: white;
    box-shadow: 0 1px 1px rgba(0,0,0,0.15);
  }
  position: relative;
  .sidebar-tree {
    height: 100vh;
    width: 25rem;
    position: absolute;
    padding: .5rem;
    .question-chapter-selector {
      height: 85vh;
    }
  }
  .sidebar-tree.fixed {
    position: fixed;
    top: 0;
  }
  .question-container {
    min-height: 100vh;
    margin-left: 26rem;
    width: ~"calc(100% - 26rem)"
  }
  .question-list {
    padding: 1rem;
    .state-3004 {
      color: #aaa;
    }
    .question-abstract {
      &:hover, &:focus, &:active {
        cursor: pointer;
        color: @color-blue;
      }
    }
  }

  .ui.celled.table {
    font-size: .9em;
    color: #666;
    th, td {
      padding: .5em;
      &.mini {
        min-width: 3em;
      }
      &.tiny {
        min-width: 5em;
      }
      &.row-actions {
        min-width: 12em;
        text-align: right;
      }
    }
  }
}

.filter-section {
  padding: 1em;
  .pill-filter {
    padding: 0;
    margin-bottom: .5em
  }
}

.ui.label.tooltip {
  border: none;
}

</style>



