<template>
  <modal @close="$emit('close')" class="modal-default modal-large single-preview-modal" with-footer="false">
    <h4 slot="header">{{question.displayName}}-预览</h4>
    <div slot="body">
      <div class="local-audio" v-if="localAudioFile">
        <audio controls class="local-audio-preview" controlsList="nodownload"></audio>
        <span class="text-muted"> 选中的本地听力文件（未上传） </span>
      </div>
      <component
        :is="currentView(question)"
        :question="question"
        :showDisplayName="false"
        :localAudioInstead="Boolean(this.localAudioFile)"
        :key="question.uuid"></component>
    </div>
  </modal>
</template>

<script>
import Modal from 'components/modal'
import { getViewByDkind } from '@/question-bank/preview/utils'

export default {
  name: 'single-preview-modal',
  props: {
    question: { type: Object, required: true },
    analysisFolded: { type: Boolean, default: false },
    localAudioFile: { default: null },
  },
  data() {
    return {}
  },
  created() {
    if (this.analysisFolded != null) {
      this.question.analysisFolded = this.analysisFolded
    }
  },
  mounted() {
    if (this.localAudioFile) {
      const $audio = $(this.$el.querySelector('.local-audio-preview'))
      const reader = new FileReader()
      reader.onload = (e) => {
        $audio.attr('src', e.target.result)
        $audio.play()
      }
      reader.readAsDataURL(this.localAudioFile)
    }
    this.$nextTick(() => {
      MathJax.Hub.Queue(['Typeset', MathJax.Hub])
    })
  },
  components: {
    Modal,
  },
  methods: {
    currentView(question) {
      return getViewByDkind(question.displayKind)
    },
  },
}
</script>

<style lang="less">
@import "~@/question-bank/preview/index.less";
</style>

<style lang="less" scoped>
.single-preview-modal {
  font-family: Palatino, Times, Arial, SimSun, "Microsoft YaHei", "微软雅黑", STHeiti, sans-serif;
  line-height: 1.8;
  height: 100%;
  font-size: 14px;
  padding: 1rem;
}
.local-audio {
  padding-bottom: 1em;
  margin-bottom: 1em;
  border-bottom: solid 1px #D3DCE6;
}
</style>



