<template>
  <div class="preview-question preview-matching">
    <div class="q-content">
      <span v-if="index" class="seq-label main-seq-label">{{index}}. </span>
      <span v-if="showDisplayName" class="display-name-label">{{question.displayName}}</span>
      <span v-if="question.score">({{question.score}}分)</span>
      <span v-html="question.stemHtml" class="richtext"></span>
      <div class="display-flex option-answer">
        <div class="option-pool flex-1">
          <span class="section-sm-label">备选项</span>
          <div class="display-flex" v-for="option of question.options" v-bind:key="option.id">
            <span class="seq-label">{{option.seqLabel}}. </span>
            <span v-html="option.textHtml" class="richtext"></span>
          </div>
        </div>
        <div class="sub-questions" v-if="showAnswer">
          <span class="text-label">各小题作答详情：</span>
          <div class="sub-question" v-for="(subQ, index) of question.questions" v-bind:key="subQ.id">
            <span class="subq-seqlabel">({{index + 1}})</span>
            <span class="user-answer" v-if="showUserAnswer">
              <span class="text-label text-danger" v-if="!subQ.getUserAnswerOption(studentId)">未作答</span>
              <span v-else :class="subQ.getUserAnswerOption(studentId).isAnswer ? 'user-correct':'user-wrong'">
                {{subQ.getUserAnswerOption(studentId).seqLabel}}
                <icon :name="subQ.getUserAnswerOption(studentId).isAnswer?'check':'remove'"></icon>
              </span>
            </span>
            <span class="text-label">正确答案 </span>
            <div class="subq-content flex-1"> {{subQ.answerSeqLabel}} </div>
            <div class="q-stat" v-if="showStat && question.hasReport">
              <div class="stat-chart">
                <circular-chart class="normal content"
                  :options="subQ.options"
                  :totalStuCount="Object.keys(allStudents).length"></circular-chart>
                <div class="overlay content"></div>
                <button class="ui primary button action content" @click="resultModalTarget=subQ">查看详情</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <toggle-analysis
      v-if="!answerSheetMode"
      :question="question"
      :unfold="unfoldAnalysis"></toggle-analysis>
    <modal-objective-results
      :question="resultModalTarget"
      :allStudents="allStudents"
      v-if="showStat && resultModalTarget && resultModalTarget.hasReport"
      @close="resultModalTarget=null"></modal-objective-results>
  </div>
</template>

<script>
/*
 showAnswer > showStat 因为展示统计结果等于间接的展示答案
 showAnswer 目前是在预览文件时用到，不涉及到学生作答，暂定 showAnswer > showUserAnswer
 */
import CircularChart from 'components/questions/circular-chart'
import ModalObjectiveResults from 'components/questions/modal-objective-results'
import Icon from 'components/icons/Icon'
import 'components/icons/slim-remove'
import { Q_MATCHING } from '../consts'
import ToggleAnalysis from './toggle-analysis'

export default {
  name: 'preview-matching',
  props: {
    question: {
      type: Object,
      required: true,
      validate: value => value.kind === Q_MATCHING,
    },
    index: { type: Number, default: 0 },
    showDisplayName: { type: Boolean, default: true },
    unfoldAnalysis: { type: Boolean, default: false },

    showStat: { type: Boolean, required: false, default: false },
    showAnswer: { type: Boolean, default: true },
    showUserAnswer: { type: Boolean, default: false },
    studentId: { type: Number, default: null },
    answerSheetMode: { type: Boolean, default: false },
    allStudents: { type: Object, required: false, default: null },
  },
  data() {
    return {
      resultModalTarget: null,
    }
  },
  components: { ToggleAnalysis, Icon, CircularChart, ModalObjectiveResults },
}
</script>

<style lang="less" scoped>
@color-red: #dd4b39;
@color-teal: #00b5ad;

.user-answer {
  margin-right: 2em;
  font-weight: bold;
  .user-correct {
    color: @color-teal;
  }
  .user-wrong {
    color: @color-red;
  }
}
.radio-options .option-label {
  min-width: 2em;
}
.section-sm-label {
  border-bottom: solid 1px;
}

.option-pool {
  border: solid 1px #D3DCE6;
  padding: 1em;
}
.seq-label {
  width: 1.2em;
}
.option-answer {
  align-items: flex-start;
  margin-top: 1em;
  .option-pool {
    min-width: 25em;
  }
}
.sub-questions {
  margin-left: 2em;
  .q-stat {
    margin-top: -.3em;
  }
}
.sub-questions {
  min-width: 15em;
}
</style>



