<template>
<div class="bar-chart choice-chart" v-if="!blankQuestion">
  <svg version="1.1" baseProfile="full" viewBox="0 0 160 25" xmlns="http://www.w3.org/2000/svg"
    v-for="option of options" :key="option.id" :class="option.isAnswer?'correct':'wrong'">
    <rect v-if="option.multiAnswer" class="option-label" cx="10" cy="10" width="20" height="20" fill="green" />
    <circle v-else class="option-label" cx="10" cy="10" r="10" fill="green" />
    <text class="seq-label" x="10" y="10" dx="0" dy="4" text-anchor="middle" font-size="14">
      {{option.seqLabel}}
    </text>
    <rect class="progress-base" x="23" y="5" width="100" height="8" rx="4" ry="4"/>
    <rect v-if="totalStuCount" class="progress" x="23" y="5" :width="Math.round(100 * option.sids.length / totalStuCount)" height="8" rx="4" ry="4"/>
    <text x="128" y="14" font-size="12" text-anchor="start" fill="#666">{{option.sids.length}}人</text>
  </svg>
</div>
<div class="bar-chart blank-chart" v-else>
  <svg class="correct" version="1.1" baseProfile="full" viewBox="0 0 165 20" xmlns="http://www.w3.org/2000/svg">
    <text class="seq-label" x="0" y="10" dx="0" dy="4" text-anchor="start" font-size="14">对</text>
    <rect class="progress-base" x="30" y="5" width="95" height="8" rx="4" ry="4"/>
    <rect v-if="totalStuCount" class="progress" x="30" y="5" :width="Math.round(95 * blankCorrectRate)" height="8" rx="4" ry="4"/>
    <text x="128" y="14" font-size="13" text-anchor="start" fill="#666">{{blankQuestion.correctSids.length}}人</text>
  </svg>
  <svg class="wrong" version="1.1" baseProfile="full" viewBox="0 0 165 20" xmlns="http://www.w3.org/2000/svg">
    <text class="seq-label" x="0" y="10" dx="0" dy="4" text-anchor="start" font-size="14">错</text>
    <rect class="progress-base" x="30" y="5" width="95" height="8" rx="4" ry="4"/>
    <rect v-if="totalStuCount" class="progress" x="30" y="5" :width="Math.round(95 * blankWrongRate)" height="8" rx="4" ry="4"/>
    <text x="128" y="14" font-size="12" text-anchor="start" fill="#666">{{blankQuestion.wrongResults.length}}人</text>
  </svg>
</div>
</template>

<script>
export default {
  name: 'bar-chart',
  props: ['options', 'blankQuestion', 'totalStuCount'],
  data() {
    return {}
  },
  computed: {
    blankCorrectRate() {
      if (!this.blankQuestion) return 0
      return this.blankQuestion.correctSids.length / this.totalStuCount
    },
    blankWrongRate() {
      if (!this.blankQuestion) return 0
      return this.blankQuestion.wrongResults.length / this.totalStuCount
    },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";
.choice-chart, .blank-chart {
  min-width: 140px;
  width: 100%;
  height: auto;
  font-family: @base-font-family;
  font-size: 14px;
  .progress-base {
    fill: @color-light-gray;
  }
  .seq-label {
    fill: @color-light-black;
  }
}
.choice-chart .correct .seq-label {
  fill: @color-white;
}

.correct {
  .option-label {
    fill: @color-teal;
  }
  .progress {
    fill: @color-teal;
  }
}
.wrong {
  .option-label {
    fill: @color-light-gray;
  }
  .progress {
    fill: @color-red;
  }
}
</style>



