<template>
  <div class="question-bank" id="question-bank">
    <div class="sidebar-tree">
      <span class="text-muted" v-if="showFavorited">收藏的题目不支持章节/知识点筛选</span>
      <div v-else>
        <div v-show="treeType==='chapter'">
          <div class="sidebar-title">
            <span>根据章节筛选</span>
            <span class="link search link-primary" v-if="!searchMode" @click="searchMode=true"><i class="search icon"></i></span>
            <span class="link link-primary" v-if="searchMode" @click="searchMode=false">返回章节树</span>
          </div>
          <v-select
            :on-search="searchChapter"
            :options="options"
            :on-change="searchChapterIdChanged"
            placeholder="输入关键词，搜索所有教材的章节"
            v-show="searchMode">
          </v-select>
          <chapter-selector
            bookSource="question"
            :onlyLeafCanSelect="false"
            @selectChapter="chapterId=>selectedChapterId=chapterId"
            key="question-chapter-selector"
            class="question-chapter-selector"
            v-show="!searchMode"
            ></chapter-selector>
        </div>
        <div v-show="treeType==='knowledge'">
          <div class="sidebar-title">
            <span>根据知识点筛选</span>
          </div>
          <knowledge-selector
            key="sidebar-selector"
            :mustSelectOne="false"
            :initFirstSelected="false"
            :onlyLeafCanSelect="false"
            :openLevel="2"
            class="question-knowledge-selector"
            @selectKnowledge="knowledgeId=>selectedKnowledgeId=knowledgeId"
            v-show="!searchMode"
            ></knowledge-selector>
        </div>
      </div>
      <div class="question-bucket">
        <div class="stat-labels">
          <span v-for="(count, dKind) in bucketKindCount" class="ui label" v-if="count" v-bind:key="dKind">{{dkindInfoMap[dKind].displayName}}<div class="detail">{{count}}</div></span>
        </div>
        <button class="ui primary button" @click="showModalPaper">试题篮 [{{bucketQuestionCount}}] / 编辑预览</button>
      </div>
    </div>
    <div class="question-container" id="question-container">
      <div class="filter-section">
        <div v-if="teacher" class="pill-filter">
          题型：
          <span @click="displayKind=0" class="filter-label" :class="displayKind===0?'active':''">全部</span>
          <span v-for="dKind of questionDkinds" v-bind:key="dKind.displayKind" @click="displayKind=dKind.displayKind" class="filter-label" :class="displayKind===dKind.displayKind?'active':''">{{dKind.displayName}}</span>
        </div>
        <div class="pill-filter">
          难度：
          <span @click="difficulty=0" class="filter-label"
           :class="difficulty===0?'active':''">全部</span>
          <span v-for="d of difficulties" @click="difficulty=d.value" class="filter-label"
           :class="d.value===difficulty?'active':''" v-bind:key="d.difficulty">{{d.label}}</span>
        </div>
        <div class="pill-filter">
          来源：
          <span @click="gallerySource=0" class="filter-label"
           :class="gallerySource===0?'active':''">题库</span>
          <span @click="gallerySource=1" class="filter-label"
           :class="gallerySource===1?'active':''">我创建的</span>
        </div>
      </div>
      <div class="question-list">
        <loader v-if="loading"></loader>
        <template v-else>
          <div v-for="question of questions" class="question-block" @click="question.analysisFolded = !question.analysisFolded" v-bind:key="question.uuid">
            <div class="question-header borderless ui menu">
              <div class="item item-name">
                {{question.displayName}}
              </div>
              <div class="right menu">
                <div class="item blue" @click.stop="openDuplicateQuestion(question)">
                  <i class="copy outline icon"></i>复制
                </div>
                <button class="ui icon yellow basic button" @click.stop="undoFavorite(question)" v-if="question.favorited" data-tooltip="取消收藏" data-position="bottom right" data-variation="mini" data-inverted="">
                  <i class="star icon"></i>
                </button>
                <button class="ui icon basic button" @click.stop="favorite(question)" v-else data-tooltip="收藏" data-position="bottom right" data-variation="small" data-inverted="">
                  <i class="empty star icon"></i>
                </button>
                <div class="item red" v-if="checkInBucket(question)" @click.stop="removeFromBucket(question)">
                  <i class="remove icon"></i> 移出组卷
                </div>
                <div class="item blue" v-else @click.stop="addInBucket(question, $event)">
                  <i class="add icon"></i> 加入组卷
                </div>
              </div>
            </div>
            <component
              :is="currentView(question)"
              :question="question"
              :show-display-name="false"
              :key="question.uuid"></component>
          </div>
        </template>
      </div>
      <div class="content-empty" v-if="!loading && questions.length===0">
        <img :src="require('assets/img/content-empty.png')">
        <p>
          什么也没有，试试<span class="text-danger">减少筛选条件</span>
        </p>
      </div>
      <pagination class="pagination"
        :totalPage="totalPage" :curPage="selectedPage"
        @update:selected="page=>fetchQuestions(page)" v-show="!loading && questions.length"></pagination>
    </div>

    <page-modal v-if="modalPaper" @close="modalPaper=false" class="modal-paper" :class="bucketQuestions.length?'modal-massive':''" key="modal-paper">
      <template slot="content">
        <div class="paper-content" v-if="bucketQuestions.length">
          <div class="question-list">
            <div v-for="(question, index) of bucketQuestions" class="question-block" v-bind:key="question.uuid">
              <div class="question-header borderless tiny ui menu">
                <div class="item item-name">
                  {{index+1}}. {{question.displayName}}
                </div>
                <div class="item muted">
                  每小题<input class="set-score" type="number" v-model.number="question.subScore" v-on:input="($event)=>changeQuestionScore(question, $event)" min="0" max="100">分 / 本题共{{question.subScore * question.subQuestionCount}}分
                </div>
                <div class="right menu">
                  <div class="item" :class="index===0?'disabled':''" @click="qForward(question)">
                    <i class="arrow up icon"></i>上移
                  </div>
                  <div class="item" :class="index===bucketQuestions.length-1?'disabled':''" @click="qBackward(question)">
                    <i class="arrow down icon"></i> 下移
                  </div>
                  <div class="item red" @click="removeFromBucket(question)">
                    <i class="remove icon"></i> 移出组卷
                  </div>
                </div>
              </div>
              <component
                :is="currentView(question)"
                :question="question"
                :show-stat="false"
                :show-display-name="false"
                :key="question.key"></component>
            </div>
          </div>
          <div class="modal-paper-sidebar">
            <h3>组卷：<span>共{{bucketQuestions.length}}大题，总分{{bucketTotalScore}}分</span></h3>
            <table class="ui compact table">
              <thead>
                <tr class="center aligned">
                  <th>序号</th>
                  <th>题型</th>
                  <th>小题分数</th>
                  <th>本题总分</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(q, idx) of bucketQuestions" class="center aligned" v-bind:key="q.uuid">
                  <td>{{idx + 1}}</td>
                  <td>{{dkindInfoMap[q.displayKind].displayName}}</td>
                  <td> <input class="set-score" type="number" :value="q.subScore" v-on:input="($event)=>changeQuestionScore(q, $event)" min="0" max="100"></td>
                  <td>{{q.subScore * q.subQuestionCount}}</td>
                </tr>
                <tr class="center aligned">
                  <td></td>
                  <td>共{{bucketQuestions.length}}大题</td>
                  <td></td>
                  <td>总分{{bucketTotalScore}}分</td>
                </tr>
              </tbody>
            </table>
            <div>
              <button class="ui tiny blue button pull-right" @click="showModalPaperPublish">确定，下一步</button>
              <span class="link link-primary" @click="importPaperData.modalOpened=true" v-if="teacher.isSubjectMaster">从已有试题组导入题目</span>
            </div>
          </div>
        </div>
        <div class="content-empty" v-else>
          <img :src="require('assets/img/content-empty.png')">
          <p>还没有题目，先添加几题吧。
            <span class="link link-primary" @click="importPaperData.modalOpened=true" v-if="teacher.isSubjectMaster">或从已有试题组导入题目</span>
          </p>
        </div>
      </template>
    </page-modal>

    <page-modal v-if="importPaperData.modalOpened" class="modal-padded import-paper-modal" @close="closeImportPaperModal">
      <template slot="content">
        <h3>
          从已有试题组导入题目<br>
          <span class="text-muted text-small text-normal">只能导入自己学科的题目。若试题组包含其他老师独立录入的题目（即非系统题库中的题），将被过滤掉。</span>
        </h3>
        <div class="ui small form">
          <div class="field inline required">
            <label class="text-muted">要导入的试题组ID</label>
            <input type="number" v-model="importPaperData.fromPaperId" placeholder="试题组ID必填">
            <span class="text-muted">{{importPaperData.paper && '试题组：' + importPaperData.paper.name || '未指定有效的试题组'}}</span>
            <table v-if="importPaperData.paper" class="ui compact table">
              <thead>
                <tr>
                  <th>序号</th>
                  <th>题型</th>
                  <th>系统题库/老师创建</th>
                  <th>是否会被导入</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(q, index) in importPaperData.paper.questions">
                  <td>{{index + 1}}</td>
                  <td>{{q.displayName}}</td>
                  <td>{{q.authorId && '老师' || '系统'}}</td>
                  <td>
                    <template v-if="checkInBucket(q)">
                      <i class="ui icon red close"></i>已在试题篮中
                    </template>
                    <template v-else-if="q.authorId">
                      <i class="ui icon red close" v-if="q.authorId"></i>老师创建
                    </template>
                    <i class="ui icon teal check" v-else></i>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div v-if="showImportPaperButton">
            <button class="ui tiny blue button" @click="addPaperQuestionsIntoBucket">导入题目列表</button>
            <button class="ui tiny button" @click="closeImportPaperModal">取消</button>
          </div>
        </div>
      </template>
    </page-modal>

    <page-modal
      v-if="modalPaperPublish"
      @close="modalPaperPublish=false"
      class="modal-padded modal-paper modal-paper-publish"
      key="modal-paper-publish">
      <template slot="content">
        <h3>生成试卷：<span>共{{bucketQuestions.length}}大题，总分{{bucketTotalScore}}分</span></h3>
        <span class="text-muted">创建成功的试卷将放入 备课->我的->我的试题组 中</span>
        <div>
          <div class="paper-form ui form">
            <div class="field">
              <label for="paper-title">试卷题目*</label>
              <input type="text" name="paper-title" v-model="paperTitle" placeholder="不超过100个字符">
            </div>
            <div class="field">
              <label>为本卷关联章节（可不填）</label>
              <chapter-selector
                bookSource="platform"
                key="platform-chapter-selector"
                @selectChapter="chapterId=>paperChapterId=chapterId"></chapter-selector>
            </div>
            <div class="field">
              <button class="ui tiny button" @click="showModalPaper">上一步</button>
              <button class="ui tiny blue button" @click="createPaper($event)">生成试卷</button>
            </div>
          </div>
        </div>
      </template>
    </page-modal>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import _ from 'lodash'
import Pagination from 'components/pagination'
import ChapterSelector from 'components/chapter-selector'
import KnowledgeSelector from 'components/knowledge-selector'
import Modal from 'components/modal'
import PageModal from 'components/page-modal'
import Loader from 'components/loader'
import { DIFFICULTIES, Q_DISPLAY_KIND_MAP, STATE_APPROVED } from '@/question-bank/consts'
import { getModelByDkind } from '@/question-bank/utils'
import { getViewByDkind } from '@/question-bank/preview/utils'
import { pathConvert } from '@/assets/js/utils'

const qs = require('qs')

export default {
  name: 'question-bank',
  props: {},
  data() {
    return {
      difficulties: DIFFICULTIES,
      loading: false,
      questionSource: 'bank',
      questions: [],
      totalPage: 0,
      selectedPage: 1,
      searchName: '',
      options: [],
      searchMode: false,
      selectedChapterId: 0,
      selectedKnowledgeId: 0,
      difficulty: 0,
      dkindInfoMap: Q_DISPLAY_KIND_MAP,
      displayKind: 0,
      bucketQuestions: [],
      bucketKindCount: {},
      modalPaper: false,
      modalPaperPublish: false,
      paperTitle: '',
      paperChapterId: 0,
      treeType: this.$route.query.tree_type,
      gallerySource: 0,
      importPaperData: {
        fromPaperId: 0,
        paper: null,
        modalOpened: false,
      },
    }
  },
  components: {
    Pagination,
    PageModal,
    Modal,
    ChapterSelector,
    KnowledgeSelector,
    Loader,
    vSelect: () => import(/* webpackChunkName: "vue-select" */ 'vue-select'),
  },
  created() {
    let localUuidScores = localStorage.getItem(`${this.teacher.id}-new-question-uuids`)
    localUuidScores = localUuidScores && JSON.parse(localUuidScores) || []
    const uuids = []
    const uuidScoreMap = {}
    for (const uuidScore of localUuidScores) {
      uuids.push(uuidScore[0])
      uuidScoreMap[uuidScore[0]] = uuidScore[1]
    }

    if (uuids.length) {
      axios.get('/question/j/questions/retrieve', { params: { uuids } }).then((response) => {
        if (response.data.status === 0) {
          for (const questionData of response.data.questions) {
            const QuestionModel = getModelByDkind(questionData.displayKind)
            const question = new QuestionModel({
              sourceAction: 'preview',
              ...questionData,
            })
            question.subScore = uuidScoreMap[question.uuid]
            this.bucketQuestions.push(question)
            const dkind = question.displayKind
            if (this.bucketKindCount.hasOwnProperty(dkind)) {
              this.bucketKindCount[dkind] += 1
            } else {
              this.$set(this.bucketKindCount, dkind, 1)
            }
          }
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail)
        }
      })
    }
  },
  mounted() {
    this.fetchQuestions()
    this.fetchCopiedQuestions()
    $(window).scroll(() => {
      const windowTop = $(window).scrollTop()
      if (windowTop >= 118) {
        $('.sidebar-tree').addClass('fixed')
      }
      if (windowTop < 118) {
        $('.sidebar-tree').removeClass('fixed')
      }
    })
  },
  watch: {
    'importPaperData.fromPaperId': function (val) {
      if (val) this.fetchAndCheckImportPaper(val)
      else this.importPaperData.paper = null
    },
    $route: function () {
      this.treeType = this.$route.query.tree_type
      if (this.treeType === 'knowledge') {
        this.searchMode = false
      }
    },
    selectedChapterId() {
      if (this.questionSource === 'bank') {
        this.fetchQuestions()
      }
    },
    questionSource() { this.fetchQuestions() },
    difficulty() { this.fetchQuestions() },
    displayKind() {
      this.fetchQuestions()
    },
    selectedKnowledgeId() {
      this.fetchQuestions()
    },
    gallerySource() {
      this.fetchQuestions()
    },
    loading(val, oldVal) {
      if (val && !oldVal) {
        const windowTop = $(window).scrollTop()
        if (windowTop >= 118) {
          $(window).scrollTop(118)
        }
      }
    },
    modalPaper: function (val) {
      if (val) {
        this.$nextTick(() => { MathJax.Hub.Queue(['Typeset', MathJax.Hub]) })
      } else {
        this.saveLocalUuidScores()
      }
    },
  },
  computed: {
    showImportPaperButton() {
      const paper = this.importPaperData.paper
      return paper && paper.questions.some(q => q.authorId === 0)
    },
    showFavorited() {
      return this.questionSource === 'favorited'
    },
    bucketQuestionCount() {
      return this.bucketQuestions.length
    },
    bucketTotalScore() {
      let score = 0
      for (const q of this.bucketQuestions) {
        score += (q.subScore * q.subQuestionCount || 0)
      }
      return score
    },
    ...mapState(['teacher', 'questionDkinds']),
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (!['chapter', 'knowledge'].includes(to.query.tree_type)) {
        vm.$router.replace({ name: 'bank-gallery', query: { tree_type: 'chapter' } })
      }
    })
  },
  methods: {
    fetchAndCheckImportPaper(paperId) {
      const url = `/question/j/papers/${paperId}`
      axios.get(url).then(resp => {
        if (resp.data.status === 0) {
          const paper = resp.data.paper
          if (resp.data.paper.subjectId !== this.teacher.subjectId) {
            this.$toasted.error('试卷和你的学科不匹配', { position: 'top-center' })
            this.importPaperData.fromPaperId = 0
            return
          }
          this.importPaperData.paper = resp.data.paper
          const questions = []
          for (const questionData of paper.questions) {
            const QuestionModel = getModelByDkind(questionData.displayKind)
            questions.push(new QuestionModel(questionData))
          }
          this.importPaperData.paper = {
            ...paper,
            questions,
          }
        } else if (resp.data.status === 1) {
          this.importPaperData.fromPaperId = 0
          this.$toasted.error(resp.data.detail, { position: 'top-center' })
        }
      })
    },
    addPaperQuestionsIntoBucket() {
      const paper = this.importPaperData.paper
      if (!paper) {
        this.$toasted.error('找不到对应试题组')
        return
      }
      for (const question of paper.questions) {
        if (question.authorId === 0 && !this.checkInBucket(question)) {
          this.$set(question, 'subScore', question.score / question.subQuestionCount)
          this.bucketQuestions.push(question)
          const dkind = question.displayKind
          if (dkind in this.bucketKindCount) {
            this.bucketKindCount[dkind] += 1
          } else {
            this.$set(this.bucketKindCount, dkind, 1)
          }
        }
      }
      this.saveLocalUuidScores()
      this.$toasted.success('添加成功')
      this.closeImportPaperModal()
    },
    closeImportPaperModal() {
      this.importPaperData.fromPaperId = 0
      this.importPaperData.modalOpened = false
    },
    openDuplicateQuestion(question) {
      const routeData = this.$router.resolve({
        name: 'duplicate-question',
        params: { uuid: question.uuid },
      })
      window.open(routeData.href, '_blank');
    },
    saveLocalUuidScores() {
      const uuidScores = []
      for (const q of this.bucketQuestions) {
        uuidScores.push([q.uuid, q.subScore])
      }
      localStorage.setItem(`${this.teacher.id}-new-question-uuids`, JSON.stringify(uuidScores))
    },
    searchChapterIdChanged(val) {
      if (val) {
        this.selectedChapterId = val.value
      }
    },
    searchChapter(search, loading) {
      loading(true)
      this.getsearchedChpater(search, loading, this)
    },
    getsearchedChpater: _.debounce((search, loading, vm) => {
      axios.get('/pad/j/resource/chapters/search', { params: { name: search, source: 3 } }).then(response => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail)
        } else if (response.data.status === 0) {
          vm.options = response.data.chapters
        }
        loading(false)
      })
    }, 250),
    changeQuestionScore(question, event) {
      const subScore = event.target.value
      if (subScore < 0) {
        event.target.value = 0
      } else if (subScore > 100) {
        event.target.value = 100
      }
      question.subScore = event.target.value
    },
    favorite(question) {
      this.$toasted.info('暂不支持收藏题目')
    },
    undoFavorite(question) {

    },
    showModalPaper() {
      this.modalPaperPublish = false
      this.$nextTick(() => { this.modalPaper = true })
    },
    showModalPaperPublish() {
      if (this.bucketTotalScore > 300) {
        this.$toasted.error('组卷总分不能超过 300 分')
        return
      }
      this.modalPaper = false
      this.$nextTick(() => { this.modalPaperPublish = true })
    },
    qForward(question) {
      const index = this.bucketIndexOf(question)
      if (index === 0) return
      this.bucketQuestions.splice(index, 1)
      this.bucketQuestions.splice(index - 1, 0, question)
    },
    qBackward(question) {
      const index = this.bucketIndexOf(question)
      if (index === this.bucketQuestions.length - 1) return
      this.bucketQuestions.splice(index, 1)
      this.bucketQuestions.splice(index + 1, 0, question)
    },
    addInBucket(question, event) {
      if (!this.checkInBucket(question)) {
        if (event) {
          const qElement = $(event.target).parents('.question-block').find('.preview-question').eq(0)
          const qClone = qElement.clone()
          const bucket = $('.question-bucket')
          qClone.appendTo($('body')).css({
            position: 'absolute',
            top: qElement.offset().top,
            left: qElement.offset().left,
            backgroundColor: 'white',
            border: 'solid 1px #ccc',
          }).animate({
            top: bucket.offset().top + bucket.innerHeight() / 2,
            left: bucket.offset().left + bucket.innerWidth() / 2,
            width: 2,
            height: 2,
            opacity: 0.5,
          }, 500, () => { qClone.detach() })
          bucket.animate({ opacity: 0.8 }, 500).animate({ opacity: 1 }, 500)
        }
        this.$set(question, 'subScore', this.dkindInfoMap[question.displayKind].score)
        this.bucketQuestions.push(question)
        const dkind = question.displayKind
        if (this.bucketKindCount.hasOwnProperty(dkind)) {
          this.bucketKindCount[dkind] += 1
        } else {
          this.$set(this.bucketKindCount, dkind, 1)
        }
        this.saveLocalUuidScores()
      }
    },
    removeFromBucket(question) {
      const index = this.bucketIndexOf(question)
      if (index < 0) return
      this.bucketQuestions.splice(index, 1)
      this.saveLocalUuidScores()
      this.bucketKindCount[question.displayKind] -= 1
    },
    bucketIndexOf(question) {
      const len = this.bucketQuestions.length
      for (let i = 0; i < len; i++) {
        if (this.bucketQuestions[i].uuid === question.uuid) {
          return i
        }
      }
      return -1
    },
    checkInBucket(question) {
      return this.bucketIndexOf(question) >= 0
    },
    currentView(question) {
      return getViewByDkind(question.displayKind)
    },
    fetchCopiedQuestions() {
      const { query: { id } } = this.$route;
      if (id === undefined) return
      axios
        .get(`/question/j/papers/${id}/questions`)
        .then(({ data: { status, questions, detail } }) => {
          if (status) return new Error(detail)
          this.$toasted.info('复制试题组成功')
          questions.map(ele => {
            const QuestionModel = getModelByDkind(ele.displayKind)
            return new QuestionModel(ele)
          }).forEach(question => {
            this.addInBucket(question)
          })
          this.$nextTick().then(() => {
            MathJax.Hub.Queue(['Typeset', MathJax.Hub])
          })
          // this.showModalPaper()
        })
        .catch((e) => {
          this.$toasted.error(e.message || '复制试题组失败')
        })
    },
    fetchQuestions(page = 1) {
      let url = ''
      const params = {
        page,
        pageSize: 15,
        displayKind: this.displayKind,
        difficulty: this.difficulty,
      }
      if (this.treeType === 'knowledge') {
        params.knowledgeId = this.selectedKnowledgeId
      } else if (this.treeType === 'chapter') {
        params.chapterId = this.selectedChapterId
      }

      if (this.gallerySource === 1) {
        url = '/question/j/questions/mine'
        params.state = STATE_APPROVED
      } else if (this.gallerySource === 0) {
        url = '/question/j/questions/gallery'
      }

      this.loading = true
      return axios.get(url, { params }).then(response => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail)
          this.loading = false
        } else if (response.data.status === 0) {
          this.questions = []
          for (const questionData of response.data.questions) {
            const QuestionModel = getModelByDkind(questionData.displayKind)
            this.questions.push(new QuestionModel(questionData))
          }
          this.totalPage = response.data.totalPage
          this.selectedPage = page
          this.loading = false
          this.$nextTick().then(() => {
            MathJax.Hub.Queue(['Typeset', MathJax.Hub])
          })
        }
      })
    },
    selectNodes(nodes) {
      if (nodes.length) {
        this.selectedChapterId = nodes[0].id
      } else {
        this.selectedChapterId = 0
      }
    },
    createPaper(event) {
      const paperTitleLength = this.paperTitle.length
      if (paperTitleLength <= 0 || paperTitleLength >= 100) {
        this.$toasted.error('试卷标题必填，且不能超过100个字符')
      } else {
        $(event.target).addClass('loading')
        const questions = []
        for (const q of this.bucketQuestions) {
          questions.push([q.uuid, q.subScore])
        }
        const params = {
          questions: JSON.stringify(questions),
          title: this.paperTitle,
          chapterId: this.paperChapterId,
          totalScore: this.bucketTotalScore,
        }
        axios.post('/question/j/papers/create', qs.stringify(params)).then((response) => {
          if (response.data.status === 1) {
            this.$toasted.error(response.data.detail)
            $(event.target).removeClass('loading')
          } else if (response.data.status === 0) {
            this.$toasted.info('创建成功，试卷已放入 备课->我的->我的试题组 中', {
              duration: 1000,
              position: 'bottom-center',
              onComplete: () => {
                this.bucketQuestions = []
                this.bucketKindCount = {}
                this.paperTitle = ''
                this.selectedChapterId = 0
                this.modalPaperPublish = false
                localStorage.setItem(`${this.teacher.id}-new-question-uuids`, JSON.stringify([]))
                $(event.target).removeClass('loading')
                window.location.href = pathConvert('resource', 'mine/new-papers')
              },
            })
          }
        })
      }
    },
  },
}
</script>

