<template>
  <div class="edit-question-form edit-objective-blank">
    <div class="left-panel">
      <div class="qrow">
        <span class="qrow-label required">题干</span>
        <div class="qrow-content">
          <text-editor v-model="question.stemRich" :supportBlankBlot="true"></text-editor>
          <div class="text-editor-extra">
            <span class="ui tiny basic primary button add-question" @click="insertBlankPlaceholder()">增加小题</span>
            <span class="text-muted">空格占位符和小题强关联，删除占位符会自动删除相关小题。多个正确答案以$$分隔。此题型为机器自动判卷。</span>
          </div>
        </div>
      </div>
      <div class="sub-question-wrapper grey-bg" v-for="(subQ, index) in question.questions" :key="'subq-' + subQ.vkey">
        <div class="top-left-floated">
          <span class="sub-question-seq">小题 {{subQ.seq}}</span>
          <span class="link link-default link-delete" @click="delBlankPlaceholder(index + 1)">删除</span>
        </div>
        <div class="qrow">
          <span class="qrow-label required">答案</span>
          <div class="qrow-content">
            <input v-model="subQ.answerComposed" class="input-cross" placeholder="请输入答案，以$$分隔...">
          </div>
        </div>
        <div class="qrow">
          <span class="qrow-label">解析</span>
          <div class="qrow-content">
            <text-editor v-model="subQ.analysisRich" :nRow="2"></text-editor>
          </div>
        </div>
      </div>
    </div>
    <div class="right-panel flex-1">
      <common-info :question="question"></common-info>
      <button class="ui primary button" @click="saveQuestion">保存</button>
      <button class="ui button" @click="modalOpen=true">预览</button>
      <single-preview-modal
        v-if="modalOpen"
        @close="modalOpen=false"
        :question="question">
      </single-preview-modal>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'

import SinglePreviewModal from '@/question-bank/preview/single-preview-modal'
import TextEditor from 'components/text-editor'
import { ObjectiveBlankQuestion } from '@/question-bank/objective-blank'
import CommonInfo from './common-info'

const qs = require('qs')

export default {
  name: 'edit-objective-blank',
  props: {
    question: { type: Object, default: null },
    action: { type: String, required: true }, // 'create'  'edit' 'duplicate'
  },
  data() { return { modalOpen: false } },
  components: { TextEditor, CommonInfo, SinglePreviewModal },
  mounted() {
    this.$eventHub.$on('insert-blank-question', (seq) => {
      this.insertNewSubQ(seq)
    })
    this.$eventHub.$on('del-blank-questions', (seqs) => {
      const decreaseSeqs = seqs.sort((a, b) => a < b)
      for (const seq of decreaseSeqs) this.delSubQ(seq)
    })
  },
  computed: {
    ...mapState(['teacher']),
  },
  methods: {
    insertNewSubQ(seq) {
      for (let i = 1; i <= this.question.questions.length; i++) {
        if (i >= seq) {
          this.question.questions[i - 1].seq = i + 1
        }
      }
      const newSubQ = new ObjectiveBlankQuestion({ seq, vkey: this.question.increKey++ })
      this.question.questions.splice(seq - 1, 0, newSubQ)
    },
    saveQuestion() {
      this.question.subjectId = this.teacher.subjectId
      const error = this.question.validate()
      if (error) {
        this.$toasted.error(error[1], { duration: 1000, position: 'bottom-center' })
        this.$store.commit('changeCheckSaving', true)
        this.$eventHub.$emit('save-question')
      } else {
        const url = this.question.uuid ? '/question/j/questions/edit' : '/question/j/questions/create'
        axios.post(url, qs.stringify({ questionData: this.question.stringify() })).then((response) => {
          if (response.data.status === 0) {
            this.$toasted.info(this.action === 'create' && '创建成功' || '已保存', {
              duration: 800,
              position: 'bottom-center',
              onComplete: () => { this.$router.push({ name: 'manage-questions' }) },
            })
          } else if (response.data.status === 1) {
            this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
          }
        })
      }
    },
    insertBlankPlaceholder() {
      this.$eventHub.$emit('insert-blank')
    },
    delBlankPlaceholder(seq) {
      this.$eventHub.$emit('del-blank', seq)
    },
    delSubQ(seq) {
      for (let i = 1; i <= this.question.questions.length; i++) {
        if (i > seq) {
          this.question.questions[i - 1].seq = i - 1
        }
      }
      this.question.questions.splice(seq - 1, 1)
    },
  },
}
</script>



