

class Overlay {
  constructor(quill, boundsContainer) {
    this.quill = quill
    this.boundsContainer = boundsContainer || document.body
    this.root = quill.addContainer(this.constructor.containerClassName)
    this.root.innerHTML = this.constructor.TEMPLATE
    // this.quill.root: div.el-editor
    // this.root: div of the overlay
    if (this.quill.root === this.quill.scrollingContainer) {
      const that = this
      this.quill.root.addEventListener('scroll', function () {
        that.root.style.marginTop = `${-1 * this.scrollTop}px`
      })
    }
  }

  hide() {
    this.root.classList.add('ql-hidden')
  }

  get hidden() {
    return this.root.classList.contains('ql-hidden')
  }

  show() {
    this.root.classList.remove('ql-hidden')
  }

  get shown() {
    return !this.hidden
  }

  position(imgNode) {
    // position the overlay over the image
    const parent = this.quill.root.parentNode
    const imgRect = imgNode.getBoundingClientRect()
    const containerRect = parent.getBoundingClientRect()
    const width = imgRect.width > 100 ? `${imgRect.width}px` : '100px'
    const height = imgRect.height > 20 ? `${imgRect.height}px` : '20px'

    Object.assign(this.root.style, {
      left: `${imgRect.left - containerRect.left - 1 + parent.scrollLeft}px`,
      top: `${imgRect.top - containerRect.top + parent.scrollTop}px`,
      width,
      height,
    })
  }

  destroy() {
    this.root.remove()
    this.quill = null
    this.boundsContainer = null
  }
}

class ImageUploadOverlay extends Overlay {
  constructor(quill, bounds) {
    super(quill, bounds)
    this.preview = this.root.querySelector('a.ql-preview')
  }
}


ImageUploadOverlay.TEMPLATE = '上传中...'
ImageUploadOverlay.containerClassName = 'ql-image-upload-overlay'

export default ImageUploadOverlay



