<template>
<page-modal
  v-if="question && question.hasReport && allStudents"
  @close="$emit('close')"
  class="modal-large">
  <div slot="content" class="modal-blank-results">
    <div class="header">
      <h3 class="title">作答详情</h3>
      <span class="text-small">{{stuWrongCount}}人错误，{{stuCorrectCount}}人正确，{{totalStuCount-stuWrongCount-stuCorrectCount}}人未作答</span>
    </div>
    <div class="section">
      <template v-if="question.wrongResults.length">
        <h4 class="title">错误学生：{{question.wrongResults.length}}人</h4>
        <table>
          <tbody>
            <tr v-for="result of question.wrongResults">
              <td class="s-name"><span>{{allStudents[result.sid].name}}</span></td>
              <td class="s-answer">{{result.answer}}</td>
            </tr>
          </tbody>
        </table>
      </template>
      <template v-if="question.correctSids.length">
        <h4 class="title">正确学生：{{question.correctSids.length}}人</h4>
        <student-labels
          :allStudents="allStudents"
          :orderedSids="question.correctSids"
          ></student-labels>
      </template>
      <div
        class="content-empty"
        v-if="!question.correctSids.length && !question.wrongResults.length">
        <img :src="require('assets/img/content-empty.png')">
        <p>本题还没有人作答</p>
      </div>
    </div>
  </div>
</page-modal>
</template>

<script>
import PageModal from 'components/page-modal'
import StudentLabels from 'components/student-labels'

export default {
  name: 'modal-blank-results',
  props: {
    // 一般这里是指小题
    question: { required: true },
    allStudents: { required: true },
  },
  data() {
    return {}
  },
  components: { PageModal, StudentLabels },
  computed: {
    totalStuCount() {
      if (!this.allStudents) return 0
      return Object.keys(this.allStudents).length
    },
    stuWrongCount() {
      return this.question.wrongResults.length
    },
    stuCorrectCount() {
      return this.question.correctSids.length
    },
  },
}
</script>

<style lang="less" scoped>
@import '~assets/css/base-variables.less';
.modal-blank-results {
  .header {
    padding: .7em 2em;
    background-color: @color-blue;
    color: white;
    border-bottom: 1px solid #eee;
    .title {
      margin: 0;
      font-size: 1.3rem;
    }
  }
  .section {
    min-height: 22em;
    padding: .4em 2em 2em 2em;
    .title {
      font-weight: normal;
      font-size: 1.1rem;
      margin: 1.2em 0 .3em 0;
    }
    .content-empty {
      margin-top: 3rem;
      img {
        max-width: 80px;
      }
    }
  }
}
table {
  font-size: 13px;
  width: 100%;
  tr:first-child td {
    border-top: solid 1px #eee;;
  }
  td {
    padding: .2em 1em;
    border-bottom: solid 1px #f5f5f5;
    border-left: 0;
    border-right: 0;
  }
  .s-name {
    padding: .2em .1em;
    font-size: 13px;
    width: 4.8em;
    background-color: #f5f5f5;
    span {
      width: 4.8em;
      display: inline-block;
      text-align: center;
      line-height: 1;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      vertical-align: middle;
    }
  }

}
</style>



