import SingleChoice from '@/question-bank/single-choice'
import MultiChoice from '@/question-bank/multi-choice'
import { ReadingChoice } from '@/question-bank/reading-choice'
import { Cloze } from '@/question-bank/cloze'
import ShortDialog from '@/question-bank/short-dialog'
import { LongDialog } from '@/question-bank/long-dialog'
import { ObjectiveBlank } from '@/question-bank/objective-blank'
import Essay from '@/question-bank/essay'
import TrueFalse from '@/question-bank/true-false'
import { Matching } from '@/question-bank/matching'
import {
  Q_DISPLAY_KIND_MAP,
  Q_SINGLE_CHOICE, Q_TRUE_FALSE, Q_MULTI_CHOICE, Q_OBJECTIVE_BLANK,
  Q_ESSAY, Q_READING_CHOICE, Q_CLOZE, Q_MATCHING, Q_SHORT_DIALOG, Q_LONG_DIALOG,
} from '@/question-bank/consts'


export const KIND_TO_MODEL = {
  [Q_SINGLE_CHOICE]: SingleChoice,
  [Q_MULTI_CHOICE]: MultiChoice,
  [Q_READING_CHOICE]: ReadingChoice,
  [Q_OBJECTIVE_BLANK]: ObjectiveBlank,
  [Q_CLOZE]: Cloze,
  [Q_SHORT_DIALOG]: ShortDialog,
  [Q_LONG_DIALOG]: LongDialog,
  [Q_ESSAY]: Essay,
  [Q_TRUE_FALSE]: TrueFalse,
  [Q_MATCHING]: Matching,
}

export function getModelByDkind(displayKind) {
  if (displayKind in Q_DISPLAY_KIND_MAP) {
    const kind = Q_DISPLAY_KIND_MAP[displayKind].kind
    return KIND_TO_MODEL[kind]
  }
}



