<template>
  <div class="radio-options">
    <div class="qrow">
      <span class="qrow-label required">选项</span>
      <div class="qrow-content">
        <div class="d-flex" v-for="(option, index) in options" :key="'option-' + option.vkey">
          <span class="ui radio checkbox" @click="setAnswer(option)">
            <input type="radio" :checked="option.isAnswer">
            <label>{{option.seqLabel}}</label>
          </span>
          <text-editor class="flex-1" :nRow="1" v-model="option.textRich"></text-editor>
          <i class="minus red circle icon" @click="removeOption(option)" v-show="canRemoveOption"></i>
        </div>
        <span
          v-if="canAddOption"
          class="ui tiny basic primary button add-option"
          @click="addNewOption()">增加选项</span>
      </div>
    </div>
  </div>
</template>

<script>
import TextEditor from 'components/text-editor'
import { Option } from '@/question-bank/base'

// 无论是 create 还是 edit，这里的 option 限制没有 id
export default {
  name: 'radio-options',
  props: {
    increKey: { type: Number, required: true },
    options: { type: Array, default: () => [] },
    answerSeq: { type: Number, required: true, default: 1 },
  },
  data() {
    return {}
  },
  components: { TextEditor },
  mounted() {
    while (this.options.length < 2) {
      this.addNewOption()
    }
  },
  computed: {
    canRemoveOption() {
      return this.options.length > 2
    },
    canAddOption() {
      return this.options.length < 26
    },
  },
  methods: {
    addNewOption() {
      const oldLen = this.options.length
      const option = new Option({ seq: oldLen, vkey: this.increKey })

      this.options.push(option)
      if (oldLen === 0) {
        option.isAnswer = true
        this.$emit('update:answerSeq', option.seq)
      }
      this.$emit('update:increKey', this.increKey + 1)
    },
    removeOption(option) {
      if (!this.canRemoveOption) return

      const index = this.options.indexOf(option)
      if (index >= 0) {
        this.options.splice(index, 1)
        this.restoreSeq()
        if (option.isAnswer) {
          this.restoreAnswer()
        }
      }
    },
    restoreAnswer() {
      let i = 0
      for (const option of this.options) {
        if (i === 0) {
          option.isAnswer = true
          this.$emit('update:answerSeq', 0)
        }
        i++
      }
    },
    restoreSeq() {
      const length = this.options.length
      for (let i = 0; i < length; i++) {
        this.options[i].seq = i
      }
    },
    setAnswer(option) {
      for (const option_ of this.options) {
        if (option === option_) {
          option_.isAnswer = true
          this.$emit('update:answerSeq', this.options.indexOf(option_))
        } else {
          option_.isAnswer = false
        }
      }
    },
  },
}
</script>

<style lang="less" scoped>
.radio-options .qrow{
  .minus.icon {
    cursor: pointer;
  }
}
</style>



