export function getQuestionDkindBySubjectId(subjectId) {
  const displayKinds = []
  for (const kind_ of SUBJECT_DISPLAYKIND_MAP[subjectId]) {
    displayKinds.push(Q_DISPLAY_KIND_MAP[kind_])
  }
  return displayKinds
}

