<template>
  <div class="edit-question-form edit-long-dialog">
    <div class="left-panel">
      <div class="qrow">
        <span class="qrow-label">音频</span>
        <audio-uploader class="qrow-content" ref="audio" :audioPathWrite="question.audioPathWrite"
          @uploadSucceed="fileKey => audioUploaded(fileKey)"></audio-uploader>
      </div>
      <div class="qrow">
        <span class="qrow-label">听力原文</span>
        <div class="qrow-content">
          <text-editor v-model="question.audioTextRich"></text-editor>
        </div>
      </div>
      <div class="qrow">
        <span class="qrow-label required">题干</span>
        <div class="qrow-content">
          <text-editor v-model="question.stemRich"></text-editor>
        </div>
      </div>
      <div class="sub-question-wrapper grey-bg" v-for="subQ of question.questions" :key="subQ.vkey">
        <div class="top-left-floated">
          <span class="sub-question-seq">小题 {{subQ.seq}}</span>
          <span
            v-if="canDelSubQ"
            @click="delSubQ(subQ)"
            class="link link-default link-delete"
            >删除</span>
        </div>
        <div class="qrow">
          <span class="qrow-label">题干</span>
          <div class="qrow-content">
            <text-editor v-model="subQ.textRich" :nRow="2" placeholder="输入小题题干..."></text-editor>
          </div>
        </div>
        <radio-options
          :options="subQ.options"
          :increKey.sync="subQ.increKey"
          :answerSeq.sync=subQ.answerSeq></radio-options>
        <div class="qrow">
          <span class="qrow-label">解析</span>
          <div class="qrow-content">
            <text-editor v-model="subQ.analysisRich"></text-editor>
          </div>
        </div>
      </div>
      <div class="qrow">
        <span class="qrow-label"></span>
        <div class="qrow-content">
          <span class="ui tiny basic primary button add-question" @click="addNewSubQ()">增加小题</span>
        </div>
      </div>
    </div>
    <div class="right-panel flex-1">
      <common-info :question="question"></common-info>
      <button class="ui primary button" @click="checkQuestion">保存</button>
      <button class="ui button" @click="modalOpen=true">预览</button>
      <single-preview-modal
        v-if="modalOpen"
        @close="modalOpen=false"
        :localAudioFile="$refs.audio.audioFile"
        :question="question">
      </single-preview-modal>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'

import SinglePreviewModal from '@/question-bank/preview/single-preview-modal'
import TextEditor from 'components/text-editor'
import { LongDialogQuestion } from '@/question-bank/long-dialog'
import RadioOptions from './radio-options'
import CommonInfo from './common-info'
import AudioUploader from './audio-uploader'

const qs = require('qs')

export default {
  name: 'edit-long-dialog',
  props: {
    question: { type: Object, default: null },
    action: { type: String, required: true }, // 'create'  'edit' 'duplicate'
  },
  data() {
    return { modalOpen: false }
  },
  components: { TextEditor, RadioOptions, CommonInfo, AudioUploader, SinglePreviewModal },
  mounted() {},
  computed: {
    canDelSubQ() {
      return this.subQlength > 1
    },
    subQlength() {
      return this.question.questions.length
    },
    ...mapState(['teacher']),
  },
  methods: {
    audioUploaded(fileKey) {
      this.question.audioPathWrite = fileKey
      this.saveQuestion()
    },
    checkQuestion() {
      this.question.subjectId = this.teacher.subjectId
      const error = this.question.validate()
      if (error) {
        this.$toasted.error(error[1], { duration: 1000, position: 'bottom-center' })
        this.$store.commit('changeCheckSaving', true)
        this.$eventHub.$emit('save-question')
        return
      }
      if (!this.question.audioPathWrite || this.$refs.audio.audioFile) {
        this.$refs.audio.startUploading()
      } else {
        this.saveQuestion()
      }
    },
    saveQuestion() {
      const url = this.question.uuid ? '/question/j/questions/edit' : '/question/j/questions/create'
      axios.post(url, qs.stringify({ questionData: this.question.stringify() })).then((response) => {
        if (response.data.status === 0) {
          this.$toasted.info(this.action === 'create' && '创建成功' || '已保存', {
            duration: 800,
            position: 'bottom-center',
            onComplete: () => { this.$router.push({ name: 'manage-questions' }) },
          })
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
      })
    },
    delSubQ(subQ) {
      if (!this.canDelSubQ) {
        this.$toasted.error('当前题型需要至少关联一个小题', { duration: 1000, position: 'bottom-center' })
        return
      }
      let index = this.question.questions.indexOf(subQ)
      if (index >= 0) {
        this.question.questions.splice(index, 1)
      }
      for (; index < this.subQlength; index++) {
        this.question.questions[index].seq = index + 1
      }
    },
    addNewSubQ() {
      const subQlength = this.question.questions.length
      this.question.questions.push(new LongDialogQuestion({ seq: subQlength + 1, vkey: this.question.increKey++ }))
    },
  },
}
</script>

<style lang="less" scoped>

</style>



