import SingleChoiceView from '@/question-bank/preview/single-choice'
import MultiChoiceView from '@/question-bank/preview/multi-choice'
import ReadingChoiceView from '@/question-bank/preview/reading-choice'
import ClozeView from '@/question-bank/preview/cloze'
import ShortDialogView from '@/question-bank/preview/short-dialog'
import LongDialogView from '@/question-bank/preview/long-dialog'
import ObjectiveBlankView from '@/question-bank/preview/objective-blank'
import EssayView from '@/question-bank/preview/essay'
import TrueFalseView from '@/question-bank/preview/true-false'
import MatchingView from '@/question-bank/preview/matching'

import {
  Q_DISPLAY_KIND_MAP,
  Q_SINGLE_CHOICE, Q_TRUE_FALSE, Q_MULTI_CHOICE, Q_OBJECTIVE_BLANK,
  Q_ESSAY, Q_READING_CHOICE, Q_CLOZE, Q_MATCHING, Q_SHORT_DIALOG, Q_LONG_DIALOG,
} from '@/question-bank/consts'


export const KIND_TO_VIEW = {
  [Q_SINGLE_CHOICE]: SingleChoiceView,
  [Q_MULTI_CHOICE]: MultiChoiceView,
  [Q_READING_CHOICE]: ReadingChoiceView,
  [Q_OBJECTIVE_BLANK]: ObjectiveBlankView,
  [Q_CLOZE]: ClozeView,
  [Q_SHORT_DIALOG]: ShortDialogView,
  [Q_LONG_DIALOG]: LongDialogView,
  [Q_ESSAY]: EssayView,
  [Q_TRUE_FALSE]: TrueFalseView,
  [Q_MATCHING]: MatchingView,
}

export function getViewByDkind(displayKind) {
  const kind = Q_DISPLAY_KIND_MAP[displayKind].kind
  return KIND_TO_VIEW[kind]
}



