<template>
  <div class="student-labels" :class="active?'':'inactive'">
    <template v-if="orderedSids !== null" >
      <div class="student-label" v-for="sid of orderedSids" :title="allStudents[sid].name">
        {{allStudents[sid].name}}
      </div>
    </template>
    <template v-else-if="orderedStudents !== null" >
      <div class="student-label" v-for="s of orderedStudents" :title="s.name">
        {{s.name}}
      </div>
    </template>
    <template v-else-if="orderedResults !== null && allStudents !== null">
      <div v-for="result of orderedResults" class="student-label"
        :title="allStudents[result.sid].name"
        :class="selectedItem===result?'selected':'clickable'"
        @click="$emit('selected', result)">
        {{allStudents[result.sid].name}}
      </div>
    </template>
    <template v-else-if="orderedResults !== null">
      <div v-for="s of orderedResults" class="student-label"
        :title="s.name"
        :class="selectedItem===s?'selected':'clickable'"
        @click="$emit('selected', s)">
        {{s.name}}
      </div>
    </template>
  </div>
</template>

<script>
export default {
  name: 'student-labels',
  data() {
    return {}
  },
  props: {
    // ordered* 传入一个来实现标签渲染
    allStudents: { type: Object, default: null },
    orderedSids: { type: Array, default: null }, // 学生 id
    orderedStudents: { type: Array, default: null }, // 学生 obj 数组
    orderedResults: { type: Array, default: null }, // 学生附带事件的数组，会触发 emit 时间
    selectedItem: { required: false },
    active: { type: Boolean, default: true },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";

@label-height: 1.8em;

.student-labels {
  font-size: 14px;
}

.student-label {
  padding: .1em .1em;
  margin-right: .8em;
  margin-bottom: .4em;
  font-size: 13px;
  -webkit-font-smoothing: auto;
  color: @font-color-base;
  background-color: #f1f2f3;
  border: solid 1px #eff0f1;

  line-height: @label-height;
  font-family: @base-font-family;
  display: inline-block;
  text-align: center;

  width: 4.8em;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  &.clickable {
    cursor: pointer;
  }
  &.selected {
    border-color: transparent !important;
    background-color: @color-amber-300;
    box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.1), 0px 2px 2px 0px rgba(0,0,0,0.1);
  }
}
.student-labels {
  display: flex;
  flex-wrap: wrap;
}

.student-labels.inactive .student-label {
  color: @color-grey;
  cursor: default;
  pointer-events: none;
  background-color: #f4f5f6;
  border: solid 1px #f1f2f3;
}
</style>



