<template>
  <div class="progress-bar">
    <div class="bar" :style="'transition-duration: 300ms; width: ' + percent + '%;'">
      <div v-if="showPercent && percent > 10">{{percent}}%</div>
    </div>
    <div class="label">{{label}}</div>
  </div>
</template>

<script>
// 可选 class：success, warning, danger, info
export default {
  name: 'progress-bar',
  props: {
    percent: { type: Number, default: 0 },
    label: { type: String, default: '' },
    showPercent: { type: Boolean, default: false },
  },
  data() {
    return {}
  },
}
</script>

<style lang="less" scoped>
@color-success: #4CAF50;
@color-warning: #f7ba2a;
@color-danger: #dd4b39;
@color-info: #50bfff;

.progress-bar {
  min-width: 4em;
  width: 100%;
  max-height: 2em;
  background-color: #f0f0f0;
  border-radius: .2em;
  margin: 1em 0 2.5em;
  position: relative;
  .bar {
    border-radius: .2em;
    min-height: 1em;
    height: 100%;
    position: relative;
    div {
      position: absolute;
      top: 50%;
      margin-top: -.5em;
      left: auto;
      right: .3em;
      color: white;
      text-align: right;
      font-size: .8em;
    }
  }
  .label {
    color: #666;
    position: absolute;
    width: 100%;
    top: 100%;
    left: 0;
    right: auto;
    bottom: auto;
    text-align: center;
    font-size: 1rem;
    margin-top: .5em
  }
}

.progress-bar.success .bar {
  background-color: @color-success
}
.progress-bar.warning .bar {
  background-color: @color-warning
}
.progress-bar.danger .bar {
  background-color: @color-danger
}
.progress-bar.info .bar {
  background-color: @color-info
}

</style>



