<template>
  <page-modal
    v-if="open"
    @close="$emit('close')"
    class="modal-small chapter-tag-modal">
    <template slot="content">
      <chapter-selector key="sidebar-selector"
        bookSource="platform"
        :mustSelectOne="false"
        :initFirstSelected="false"
        :multiSelect="true"
        :onlyLeafCanSelect="false"
        :openLevel="2"
        :selectedNodes="selectedChapters"
      ></chapter-selector>
      <div class="collected-nodes">
        已选章节：{{selectedChapters.length === 0 ? '无':''}}
        <span v-for="chapter of selectedChapters" class="ui chapter label">
          {{chapter.label}}<span class="action-icon" @click="removeChapter(chapter)"><icon name="slim-remove"></icon></span>
        </span>
      </div>
    </template>
  </page-modal>
</template>

<script>
import PageModal from 'components/page-modal'
import ChapterSelector from 'components/chapter-selector'
import Icon from 'components/icons/Icon'
import 'components/icons/slim-remove'

export default {
  name: 'chapter-tag-modal',
  props: {
    selectedChapters: { type: Array, default: () => [] },
    open: { type: Boolean, default: false },
  },
  data() {
    return {
    }
  },
  components: { ChapterSelector, Icon, PageModal },
  computed: {},
  methods: {
    removeChapter(chapter) {
      const index = this.selectedChapters.indexOf(chapter)
      if (index >= 0) {
        this.selectedChapters.splice(index, 1)
      }
    },
  },
}
</script>

<style lang="less">
.chapter-tag-modal.page-modal .modal-container {
  border-radius: 0;
  background-color: white;

  max-height: unset;
  padding: 0 !important;
  overflow-y: visible;

  .chapter-selector {
    margin-top: -1px;
    font-size: 12px;
    min-height: 200px;
    overflow-y: auto;
    border: none;
    background-color: #f9f9f9;
    box-shadow: inset 0 0 -1px rgba(0,0,0,.5);

    .ui.basic.button {
      border: none;
    }

    .book-tree {
      max-height: 60vh;
    }
  }
  .collected-nodes {
    padding: 1em .5em;
    min-height: 5em;
    border-top: solid 1px #ddd;
  }

}

</style>



