<template>
<div class="subjective-result" v-cloak>
  <p class="section-label text-danger" v-if="!studentResult">学生未作答</p>
  <sketch-pad
    :key="'single-' + studentResult.images[0].id"
    :result="studentResult"
    :studentName="student.name"
    :question="question"
    :canClose="false"
    v-else></sketch-pad>
</div>
</template>


<script>
require('assets/css/richtext.less')

export default {
  name: 'subjective-result',
  props: {
    question: {
      type: Object,
      required: true,
    },
    student: { type: Object, required: true },
  },
  data() {
    return {}
  },
  components: {
    'sketch-pad': () => import('components/sketch-pad.vue'),
  },
  computed: {
    studentResult() {
      if (this.question && this.question.results) {
        for (const result of this.question.results) {
          if (this.student.id === result.sid) return result
        }
      }
    },
  },
}
</script>

<style lang="less" scoped>
.section-label {
  font-family: initial;
  font-weight: bold;
  -webkit-font-smoothing: auto;
  font-size: 13px;
  margin-top: .5em;
}
</style>



