<template>
  <div class="chapter-selector" v-if="book">
    <div class="ui basic button visible">{{bookDisplay}} <icon name="angle-down"></icon></div>
    <div class="ui flowing popup top left transition">
      <div class="cascader" v-if="bookSource !=='yhfa'">
        <ul>
          <li
            v-for="(books, vName) in versionBooks"
            :key="'version-'+vName"
            @mouseenter="cascaderVersionName=vName"
            @click="cascaderVersionName=vName"
            class="expandable"
            :class="cascaderVersionName===vName?'active':''"
          ><span>{{vName}}</span><icon name="angle-right"></icon></li>
        </ul>
        <ul v-if="cascaderVersionName">
          <li
            v-for="b of versionBooks[cascaderVersionName]"
            :key="'version-'+b.id"
            @click="selectBook(cascaderVersionName, b)"
          ><span>{{b.name}}</span></li>
        </ul>
      </div>
      <div class="cascader" v-else>
        <ul>
          <li
            v-for="(vbooks, y) in versionBooks"
            :key="'year-'+y"
            @mouseenter="cascaderYear=y"
            @click="cascaderYear=y"
            class="expandable"
            :class="cascaderYear===y?'active':''"
          ><span>{{y}}</span><icon name="angle-right"></icon></li>
        </ul>
        <ul v-if="cascaderYear">
          <li
            v-for="(books, vName) in versionBooks[cascaderYear]"
            :key="'version-'+vName"
            @mouseenter="cascaderVersionName=vName"
            @click="cascaderVersionName=vName"
            class="expandable"
            :class="cascaderVersionName===vName?'active':''"
          ><span>{{vName}}</span><icon name="angle-right"></icon></li>
        </ul>
        <ul v-if="cascaderVersionName">
          <li
            v-for="b of versionBooks[cascaderYear][cascaderVersionName]"
            :key="'version-'+b.id"
            @click="selectBook(cascaderVersionName, b, cascaderYear)"
          ><span>{{b.name}}</span></li>
        </ul>
      </div>
    </div>
    <div class="book-tree">
      <loader v-if="chapterLoading"></loader>
      <template v-else>
        <pad-tree
          v-if="chapters.length"
          :treeData="chapters"
          :onlyLeafCanSelect="onlyLeafCanSelect"
          :mustSelectOne="mustSelectOne"
          :multiSelect="multiSelect"
          :initFirstSelected="initFirstSelected"
          :openLevel="openLevel"
          :selectedNodes="selectedNodes"></pad-tree>
        <div class="content-empty" v-else>
          <img :src="require('assets/img/content-empty.png')">
          <p>本教材还没有录入章节</p>
        </div>
      </template>
    </div>
  </div>
  <div class="content-empty" v-else>
    <img :src="require('assets/img/content-empty.png')">
    <p>本学科本年段没有对应的教材</p>
  </div>
</template>

<script>
import PadTree from 'components/pad-tree'
import axios from 'axios'
import Loader from 'components/loader'
import { mapState } from 'vuex'
import Icon from 'components/icons/Icon'
import 'components/icons/angle-right'
import 'components/icons/angle-down'

export default {
  name: 'chapter-selector',
  props: {
    bookSource: {
      type: String,
      default: 'platform',
      validator(val) {
        return ['platform', 'question', 'yhfa'].indexOf(val) >= 0
      },
    },
    onlyLeafCanSelect: { type: Boolean, default: true },
    mustSelectOne: { type: Boolean, default: false },
    initFirstSelected: { type: Boolean, default: false },
    multiSelect: { type: Boolean, default: false },
    openLevel: { type: Number, default: 1 },
    selectedNodes: { type: Array, default: () => [] },
  },
  data() {
    return {
      versionBooks: {},
      versionName: '',
      book: { id: 0, name: '无教材' },
      booksFetched: false,
      chapters: [],
      year: '',
      cascaderVersionName: '',
      cascaderYear: '',
      chapterLoading: false,
    }
  },
  components: { PadTree, Loader, Icon },
  mounted() {
    if (this.teacher) {
      this.book = JSON.parse(localStorage.getItem(this.bookKey)) || null
      this.versionName = localStorage.getItem(this.versionNameKey) || null
      this.year = localStorage.getItem(this.yearKey) || null
      this.fetchBooks()
    }
  },
  watch: {
    book() {
      this.fetchChapters()
    },
    teacher(val) {
      if (!val) return
      this.book = JSON.parse(localStorage.getItem(this.bookKey)) || null
      this.versionName = localStorage.getItem(this.versionNameKey) || null
      this.year = localStorage.getItem(this.yearKey) || null
      this.fetchBooks()
    },
    selectedNodes(val) {
      if (this.multiSelect) {
        this.$emit('selectChapters', val)
      } else {
        if (val.length) {
          this.$emit('selectChapter', val[0].id)
        } else {
          this.$emit('selectChapter', 0)
        }
      }
    },
  },
  computed: {
    bookDisplay() {
      if (this.versionName && this.book) {
        if (this.bookSource === 'yhfa' && this.year) {
          return `${this.year} / ${this.versionName} / ${this.book.name}`
        }
        if (['platform', 'question'].indexOf(this.bookSource) >= 0) {
          return `${this.versionName} / ${this.book.name}`
        }
      }
      return ''
    },
    versionNameKey() {
      if (!this.teacher) return ''
      return `${this.teacher.id}//-${this.bookSource}-versionname`
    },
    bookKey() {
      if (!this.teacher) return ''
      return `${this.teacher.id}//-${this.bookSource}-book`
    },
    yearKey() {
      if (!this.teacher) return ''
      return `${this.teacher.id}//-${this.bookSource}-year`
    },
    ...mapState(['teacher']),
  },
  methods: {
    fetchBooks() {
      const url = `/pad/j/resource/${this.bookSource}/books`
      return axios.get(url).then((response) => {
        this.versionBooks = response.data.bookData
      }).then(() => {
        if (['platform', 'question'].indexOf(this.bookSource) >= 0) {
          if (!this.versionName || !this.book) {
            for (const i in this.versionBooks) {
              this.versionName = i
              this.book = this.versionBooks[i][0]
              break
            }
          }
        } else if (this.bookSource === 'yhfa') {
          if (!this.year || !this.versionName || !this.book) {
            for (const year_ in this.versionBooks) {
              this.year = year_
              for (const i in this.versionBooks[year_]) {
                this.versionName = i
                this.book = this.versionBooks[year_][i][0]
                break
              }
            }
          }
        }
        this.$nextTick(() => {
          $(this.$el).find('.ui.visible.button').popup({
            on: 'click',
            inline: false,
            popup: '.flowing.popup',
          })
        })
        this.booksFetched = true
      })
    },
    fetchChapters() {
      if (!this.book || !this.book.id) {
        this.chapters = []
        return
      }
      this.chapterLoading = true
      axios.get('/pad/j/resource/chapters', { params: { bookId: this.book.id } })
        .then((response) => {
          if (response.data.status === 0) {
            this.chapters = response.data.chapterData
          } else if (response.data.status === 1) {
            this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
          }
          this.chapterLoading = false
        })
    },
    selectBook(versionName, book, year = '') {
      $('.ui.visible.button').popup('hide')
      this.versionName = versionName
      this.book = book
      localStorage.setItem(this.versionNameKey, versionName)
      localStorage.setItem(this.bookKey, JSON.stringify(book))
      if (year) {
        this.year = year
        localStorage.setItem(this.yearKey, year)
      }
    },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";
.ui.vertical.menu {
  border-radius: 0;
  width: 100%;
  display: block;
  box-shadow: none;
  margin: .5em 0;
  min-height: 2em;
  margin-bottom: -1px;
  .select-book.item {
    padding: .6em 1em;
    color: @color-blue;
    font-weight: bold;
  }
}
.ui.basic.button {
  border-radius: 0;
  box-shadow: none;
  width: 100%;
  padding: .6em 0;
  font-weight: bold;
  margin-bottom: -1px;
  border: solid 1px #ddd;
  color: @color-blue !important;
  text-overflow: left;
  .fa-icon {
    float: right;
  }
}
.ui.flowing.popup {
  padding: 0;
  .cascader {
    display: flex;
    ul {
      color: #666;
      list-style-type: none;
      padding: .5em 0;
      margin: 0;
      border-left: solid #e0e0e0 1px;
      height: 220px;
      overflow-y: auto;
      li {
        padding: .4em .5em .4em 1em;
        cursor: pointer;
        display: flex;
        span {
          width: max-content;
          min-width: 6em;
          max-width: 10em;
          margin-right: .5em;
          flex: 1;
        }
        &:hover, &:focus, &:active {
          background-color: #f9f9f9;
        }
        &.active {
          color: @color-blue;
        }
        .fa-icon {
          margin-left: 1em;
        }
      }
    }
  }
}
.book-tree {
  overflow-y: auto;
  min-height: 150px;
  border: solid 1px #ddd;
  position: relative;
}

.loader {
  left: ~"calc(50% - 30px)";
}

.content-empty {
  margin-top: 3em;
  margin-bottom: 3em;
  img {
    width: 80px;
  }
}
</style>

<style lang="less">
.book-tree {
  max-height: 100%;
}
</style>



