/* eslint-disable no-undef */
const Embed = Quill.import('blots/embed')
const Module = Quill.import('core/module')


class FormulaBlot extends Embed {
  static create(value) {
    const node = super.create(value)
    if (typeof value === 'string') {
      node.setAttribute('data-value', value)
      const mathjaxSpan = document.createElement('span')
      mathjaxSpan.classList.add('formula')
      mathjaxSpan.innerHTML = `\\(${value}\\)`
      node.appendChild(mathjaxSpan)
    }
    return node
  }

  static value(domNode) {
    return domNode.getAttribute('data-value')
  }
}

FormulaBlot.blotName = 'formula'
FormulaBlot.className = 'ql-formula'
FormulaBlot.tagName = 'SPAN'


class Formula extends Module {
  static register() {
    Quill.register(FormulaBlot, true)
  }

  constructor() {
    super()
  }
}


export { FormulaBlot, Formula as default }



