<template>
  <div class="circular-chart" v-if="options && options.length">
    <svg version="1.1" baseProfile="full" :height="40" :width="this.options.length * 32" :viewBox="viewBoxVal" xmlns="http://www.w3.org/2000/svg"
      >
      <g v-for="(option, idx) of options" :key="option.id" :class="genOptionClasses(option)">
        <circle :cx="18 + idx*40" cy="18" r="15.9155" stroke="#bbb" stroke-width=".5" fill="none" />
        <path :d="'M' + (18 + idx*40) + ' 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831'" fill="none" stroke-width="3"
        :stroke-dasharray="Math.round(100 * option.sids.length / totalStuCount) + ', 100'"
        />
        <text class="seq-label" :x="18 + idx*40" y="18" dx="0" dy="6" text-anchor="middle" fill="#666" font-size="18"> {{option.seqLabel}} </text>
        <text :x="18 + idx*40" y="36" dx="0" dy="14" text-anchor="middle" fill="#666" font-size="12"> {{option.sids.length}}人 </text>
      </g>
    </svg>
  </div>
</template>

<script>
export default {
  name: 'circular-chart',
  props: ['options', 'totalStuCount'],
  data() {
    return {}
  },
  computed: {
    viewBoxVal() {
      return `0 0 ${this.options.length * 40} 55`
    },
  },
  methods: {
    genOptionClasses(option) {
      if (option.sids.length && option.isAnswer) return 'correct'
      if (option.sids.length && !option.isAnswer) return 'wrong'
      return ''
    },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";

.circular-chart {
  font-family: @base-font-family;
}
.circular-chart .correct {
  path {
    stroke: @color-teal;
  }
  .seq-label {
    fill: @color-teal;
  }
}
.circular-chart .wrong {
  path {
    stroke: @color-red;
  }
  .seq-label {
    fill: @color-red;
  }
}
</style>



