import { BaseQuestion, Option, BaseSubQuestion, ChoiceReportMixin } from './base'
import { Q_MATCHING, SUBQ_KIND_SINGLE } from './consts'


export class MatchingQuestion extends BaseSubQuestion {
  constructor(sourceData, optionsData) {
    super(sourceData)
    this.kind = SUBQ_KIND_SINGLE
    this.analysisRich = sourceData.analysisRich || {}
    this.answerSeq = sourceData.answerSeq

    this.options = []
    if (optionsData) {
      for (const optionData of optionsData) {
        const option = new Option(optionData)
        if (option.seq === this.answerSeq) {
          option.isAnswer = true
        }
        this.options.push(option)
      }
    }
    Object.defineProperty(
      this, 'answerSeqLabel', { get() { return String.fromCharCode(65 + this.answerSeq) } })
    this.richtextPreparation()
  }

  getUserAnswerOption(studentId) {
    return this.options.find(option => option.sids.includes(studentId))
  }
}


// 先设置为 options 定长 7, quesitons 定长为 5
export class Matching extends BaseQuestion {
  constructor(sourceData) {
    super(sourceData, Q_MATCHING)
    this.structureLevel = 2
    this.stemRich = sourceData.stemRich || {}

    this.questions = []
    if (sourceData.questions) {
      const optionsData = this.onPreview && sourceData.options || []
      for (const questionData of sourceData.questions) {
        questionData.vkey = this.increKey++
        questionData.sourceAction = sourceData.sourceAction
        this.questions.push(new MatchingQuestion(questionData, optionsData))
      }
    }

    if (this.questions.length === 0 && this.onEdit) {
      for (let i = 1; i <= 5; i++) {
        this.questions.push(
          new MatchingQuestion({ seq: i, vkey: this.increKey++, sourceAction: sourceData.sourceAction }),
        )
      }
    }

    this.options = []
    if (sourceData.options) {
      for (const optionData of sourceData.options) {
        optionData.vkey = this.increKey++
        this.options.push(new Option(optionData))
      }
    }

    if (this.options.length === 0 && this.onEdit) {
      for (let i = 0; i < 7; i++) {
        this.options.push(new Option({ seq: i, vkey: this.increKey++ }))
      }
    }

    this.windup()
  }

  validate() {
    const commonError = this.commonInfoValidate()
    if (commonError) return commonError

    if (this.stemEmpty) {
      return [true, '题干不能为空']
    }

    if (this.options.length !== 7) {
      return [false, '选项个数不正确']
    }
    for (const option of this.options) {
      if (option.seq < 0 || option.seq >= 7) {
        return [false, '选项标签设置错误']
      }
      const error = option.validate()
      if (error) return error
    }

    const selectedSeq = []
    for (const subQ of this.questions) {
      if (subQ.answerSeq < 0 || subQ.answerSeq >= 7) {
        return [false, `第${subQ.seq}小题答案设置有问题`]
      }
      if (selectedSeq.includes(subQ.answerSeq)) {
        return [false, '答案设置出现重复，请修正']
      }
      selectedSeq.push(subQ.answerSeq)
    }
  }
}

Object.assign(Matching.prototype, ChoiceReportMixin)

export default Matching



