<template>
  <div class="toggle-analysis" v-if="!customSlot">
    <span class="link link-default" @click.stop="toggleContent">
      <template v-if="question.analysisFolded">
        查看解析<i class="ui angle down icon"></i>
      </template>
      <template v-else>
        收起解析<i class="ui angle up icon"></i>
      </template>
    </span>
    <div class="toggle-content" :style="question.analysisFolded?'display: none;':''">
      <div v-if="[9, 10].includes(question.kind) && !question.audioTextEmpty">
        <span class="section-sm-label">听力原文：</span>
        <span class="flex-1 richtext" v-html="question.audioTextHtml"></span>
      </div>
      <template v-if="question.hasOwnProperty('questions')">
        <div v-for="(subQ, index) of question.questions" class="display-flex">
          <span class="subq-seqlabel" v-if="hasMultiSubQ">({{index + 1}})</span>
          <div class="subq-content">
            <div v-if="'answerSeq' in subQ" class="display-flex">
              <span class="section-sm-label">答案：</span>
              <span class="flex-1 seq-label">{{subQ.answerSeqLabel}}</span>
            </div>
            <div v-if="subQ.answerComposed" class="display-flex">
              <span class="section-sm-label">答案：</span>
              <div class="flex-1">
                <span class="sub-q-answer">{{subQ.answerComposed.replace(/\$\$/g, ' 或 ')}}</span>
              </div>
            </div>
            <div v-if="!subQ.analysisEmpty" class="display-flex">
              <span class="section-sm-label">解析：</span>
              <span v-html="subQ.analysisHtml" class="richtext flex-1"></span>
            </div>
          </div>
        </div>
        <hr v-if="hasMultiSubQ">
      </template>
      <template v-else>
        <div v-if="'answerSeq' in question" class="display-flex">
          <span class="section-sm-label">答案：</span>
          <span class="flex-1 seq-label">{{question.answerSeqLabel}}</span>
        </div>
        <div v-else-if="'answerSeqComposed' in question" class="display-flex">
          <span class="section-sm-label">答案：</span>
          <span class="flex-1 seq-label">{{question.answerSeqComposed}}</span>
        </div>
        <div v-else-if="question.answerRich" class="display-flex">
          <span class="section-sm-label">答案：</span>
          <span class="flex-1 richtext" v-html="question.answerHtml"></span>
        </div>
        <div v-if="!question.analysisEmpty" class="display-flex">
          <span class="section-sm-label">解析：</span>
          <span v-html="question.analysisHtml" class="flex-1 richtext"></span>
        </div>
      </template>

      <div v-if="question.knowledges && question.knowledges.length" class="display-flex">
        <span class="section-sm-label">知识点：</span>
        <div class="flex-1">
          <span class="ui knowledge label" v-for="knowledge in question.knowledges">{{knowledge.label}}</span>
        </div>
      </div>
      <div v-if="question.chapters && question.chapters.length" class="display-flex">
        <span class="section-sm-label">章节：</span>
        <div class="flex-1">
          <span class="ui knowledge label" v-for="chapter in question.chapters">{{chapter.label}}</span>
        </div>
      </div>
      <div v-if="question.source" class="display-flex">
        <span class="section-sm-label">来源：</span>
        <span class="flex-1">{{question.source}}</span>
      </div>
    </div>
  </div>
  <div class="toggle-analysis" v-else>
    <span class="link link-default" @click="toggleContent">
      <slot name="toggleTitle"></slot><i class="ui angle down icon"></i>
    </span>
    <span class="sub-title" @click="toggleContent">
      <slot name="subTitle"></slot>
    </span>
    <div class="toggle-content" style="display: none;">
      <slot name="toggleContent"></slot>
    </div>
  </div>
</template>

<script>
/*
question: 可以是小题，也可以是大题。
*/

export default {
  name: 'toggle-analysis',
  props: {
    question: { type: Object, required: false },
    customSlot: { type: Boolean, required: false, default: false },
  },
  data() {
    return {}
  },
  computed: {
    hasMultiSubQ() {
      return this.question.questions.length > 1
    },
  },
  methods: {
    toggleContent() {
      if (this.question) {
        this.question.analysisFolded = !this.question.analysisFolded
      }
      $(this.$el.querySelector('.toggle-content')).slideToggle()
    },
  },
}
</script>

<style lang="less">
.toggle-analysis {
  hr {
    border: solid 1px #D3DCE6;
    border-top: none;
  }
  .toggle-content p {
    margin-top: 0;
  }
}

</style>

<style lang="less" scoped>
@color-blue: #42A5F5;
@base-font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;

.toggle-analysis {
  margin-top: 4px;
}

.link.link-default {
  font-size: 13px;
}
.link.link-default i.icon {
  margin-right: 0;
}

.toggle-content {
  margin-top: .5em;
  margin-bottom: .5em;
  padding: .5em 1em;
  border-left: 2px solid #D3DCE6;
  background-color: #fafafa;
}

.sub-title {
  color: @color-blue;
  float: right;
  cursor: pointer;
  font-family: @base-font-family;
  font-size: 13px;
}
</style>



