<template>
  <div class="knowledge-selector">
    <div class="book-tree">
      <loader v-if="knowledgesLoading"></loader>
      <template v-else>
        <pad-tree
          v-if="knowledges.length"
          :treeData="knowledges"
          :onlyLeafCanSelect="onlyLeafCanSelect"
          :mustSelectOne="mustSelectOne"
          :multiSelect="multiSelect"
          :initFirstSelected="initFirstSelected"
          :openLevel="openLevel"
          :selectedNodes="selectedNodes"></pad-tree>
          <div class="content-empty" v-else>
            <img :src="require('assets/img/content-empty.png')">
            <p>本教材还没有录入章节</p>
          </div>
      </template>
    </div>
  </div>
</template>

<script>
import PadTree from 'components/pad-tree'
import axios from 'axios'
import Loader from 'components/loader'
import { mapState } from 'vuex'
import Icon from 'components/icons/Icon'
import 'components/icons/angle-right'
import 'components/icons/angle-down'

export default {
  name: 'knowledge-selector',
  props: {
    onlyLeafCanSelect: { type: Boolean, default: true },
    mustSelectOne: { type: Boolean, default: false },
    initFirstSelected: { type: Boolean, default: false },
    multiSelect: { type: Boolean, default: false },
    openLevel: { type: Number, default: 1 },
    selectedNodes: { type: Array, default: () => [] },
  },
  data() {
    return {
      knowledges: [],
      knowledgesLoading: false,
    }
  },
  components: { PadTree, Loader, Icon },
  mounted() {
    this.fetchKnowledges()
  },
  watch: {
    selectedNodes(val) {
      if (this.multiSelect) {
        this.$emit('selectKnowledges', val)
      } else {
        if (val.length) {
          this.$emit('selectKnowledge', val[0].id)
        } else {
          this.$emit('selectKnowledge', 0)
        }
      }
    },
  },
  computed: {
    ...mapState(['teacher']),
  },
  methods: {
    fetchKnowledges() {
      this.knowledgesLoading = true
      axios.get('/pad/j/knowledge-tree').then((response) => {
        if (response.data.status === 0) {
          this.knowledges = response.data.knowledgeData
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
        this.knowledgesLoading = false
      })
    },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";
.book-tree {
  overflow-y: auto;
  border: solid 1px #ddd;
  min-height: 150px;
  position: relative;
}

.loader {
  left: ~"calc(50% - 28px)";
}

</style>



