<template>
  <div class="edit-question-form edit-matching">
    <div class="left-panel">
      <div class="qrow">
        <span class="qrow-label required">题干</span>
        <div class="qrow-content">
          <text-editor v-model="question.stemRich" :supportBlankBlot="true" :n-row="10"></text-editor>
          <div class="text-editor-extra">
            <span class="ui tiny basic primary button add-placeholder" @click="insertBlankPlaceholder()">插入占位符</span>
            <span class="text-muted">空格占位符和小题弱关联，注意占位符个数应等于小题数。</span>
          </div>
        </div>
      </div>
      <div class="radio-options">
        <div class="qrow">
          <span class="qrow-label required">备选项</span>
          <div class="qrow-content">
            <div class="d-flex" v-for="option of question.options" :key="option.vkey">
              <span class="seq-label option-label">{{option.seqLabel}}</span>
              <text-editor class="flex-1" :nRow="1" v-model="option.textRich"></text-editor>
            </div>
          </div>
        </div>
      </div>
      <div class="sub-question-wrapper grey-bg" v-for="subQ of question.questions">
        <div class="top-left-floated">
          <span class="sub-question-seq">小题 {{subQ.seq}}</span>
        </div>
        <div class="radio-options">
          <div class="qrow">
            <span class="qrow-label required">选项</span>
            <div class="qrow-content">
              <div class="d-flex">
                <span
                  v-for="option of question.options"
                  :key="option.vkey"
                  :class="matchOptionClass(subQ, option)"
                  @click="setAnswer(subQ, option)">
                  <input type="radio" :checked="option.seq === subQ.answerSeq">
                  <label>{{option.seqLabel}}</label>
                </span>
              </div>
            </div>
          </div>
        </div>
        <div class="qrow">
          <span class="qrow-label">解析</span>
          <div class="qrow-content">
            <text-editor v-model="subQ.analysisRich" :nRow="3"></text-editor>
          </div>
        </div>
      </div>
    </div>
    <div class="right-panel flex-1">
      <common-info :question="question"></common-info>
      <button class="ui primary button" @click="saveQuestion">保存</button>
      <button class="ui button" @click="modalOpen=true">预览</button>
      <single-preview-modal
        v-if="modalOpen"
        @close="modalOpen=false"
        :question="question">
      </single-preview-modal>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'

import SinglePreviewModal from '@/question-bank/preview/single-preview-modal'
import TextEditor from 'components/text-editor'
import CommonInfo from './common-info'
import RadioOptions from './radio-options'

const qs = require('qs')

export default {
  name: 'edit-matching',
  props: {
    question: { type: Object, default: null },
    action: { type: String, required: true }, // 'create'  'edit' 'duplicate'
  },
  data() {
    return { modalOpen: false }
  },
  components: { TextEditor, RadioOptions, CommonInfo, SinglePreviewModal },
  computed: {
    selectedSeq() {
      return this.question.questions.map(q => q.answerSeq).filter(seq => seq > 0)
    },
    ...mapState(['teacher']),
  },
  methods: {
    seqLabel(seq) {
      return String.fromCharCode(65 + seq)
    },
    setAnswer(subQ, option) {
      subQ.answerSeq = option.seq
    },
    saveQuestion() {
      const stemRich = this.question.stemRich
      const placeholders = stemRich.ops.filter(op => op.insert.hasOwnProperty('blank-placeholder'))
      if (placeholders.length !== this.question.questions.length) {
        this.$toasted.error('占位符个数应等于小题数目', { duration: 1000, position: 'bottom-center' })
        return
      }
      this.question.subjectId = this.teacher.subjectId
      const error = this.question.validate()
      if (error) {
        this.$toasted.error(error[1], { duration: 1000, position: 'bottom-center' })
        this.$store.commit('changeCheckSaving', true)
        this.$eventHub.$emit('save-question')
      } else {
        const url = this.question.uuid ? '/question/j/questions/edit' : '/question/j/questions/create'
        axios.post(url, qs.stringify({ questionData: this.question.stringify() })).then((response) => {
          if (response.data.status === 0) {
            this.$toasted.info(this.action === 'create' && '创建成功' || '已保存', {
              duration: 800,
              position: 'bottom-center',
              onComplete: () => { this.$router.push({ name: 'manage-questions' }) },
            })
          } else if (response.data.status === 1) {
            this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
          }
        })
      }
    },
    matchOptionClass(subQ, option) {
      let class_ = 'ui radio checkbox'
      if (this.selectedSeq.indexOf(option.seq) >= 0 && option.seq !== subQ.answerSeq) {
        class_ += ' disabled'
      }
      return class_
    },
    insertBlankPlaceholder() {
      this.$eventHub.$emit('insert-blank')
    },
  },
}
</script>

<style lang="less" scoped>
.radio-options .option-label {
  min-width: 2em;
}

.ui.radio.checkbox.disabled {
  pointer-events: none;
  color: #aaa;
  label {
    color: #aaa;
  }
}

</style>



