<template>
<div class="inline-subjective-result" v-cloak v-if="allStudents">
  <sketch-pad
    :key="viewStudentResult.images[0].id"
    :result.sync="viewStudentResult"
    :studentName="allStudents[viewStudentResult.sid].name"
    :question="question"
    :fixed-top="true"
    @close="toggleResultViewer"
    @succeed="jumpToNextUnReviewed"
    v-if="viewStudentResult"></sketch-pad>
  <template v-if="unreviewResults.length">
    <h3 class="ui header red">未批改<span class="note"> 点击标签批改，分数框输入回车直接提交。</span></h3>
    <student-labels
      :allStudents="allStudents"
      :orderedResults="unreviewResults"
      :selectedItem="viewStudentResult"
      @selected="toggleResultViewer"></student-labels>
  </template>
  <template v-if="reviewResults.length">
    <h3 class="ui header">已批改<span class="note"> 点击标签查看详情</span></h3>
    <student-labels
      :allStudents="allStudents"
      :orderedResults="reviewResults"
      :selectedItem="viewStudentResult"
      @selected="toggleResultViewer"></student-labels>
  </template>
  <template v-if="question.unansweredSids.length">
    <h3 class="ui header">提交未作答</h3>
    <student-labels
      :allStudents="allStudents"
      :orderedSids="question.unansweredSids"
      :active="false"></student-labels>
  </template>
  <template v-if="unsubmitedSids.length">
    <h3 class="ui header">未提交</h3>
    <student-labels
      :allStudents="allStudents"
      :orderedSids="unsubmitedSids"
      :active="false"></student-labels>
  </template>
</div>
</template>


<script>
// 以批改 sketchpad 形式查看学生的主观题作答
// 已作答区分是否批改
import _ from 'lodash'
import StudentLabels from 'components/student-labels'
import { CM_RECORD_STATUS_REVIEWED, CM_RECORD_STATUS_SUBMITTED } from 'assets/js/consts/common'

require('assets/css/richtext.less')

export default {
  name: 'inline-subjective-results',
  props: {
    question: {
      type: Object,
      required: true,
    },
    allStudents: {
      type: Object,
      required: true,
      default: null,
    },
  },
  data() {
    return {
      viewStudentResult: null,
    }
  },
  components: {
    StudentLabels,
    'sketch-pad': () => import('components/sketch-pad.vue'),
  },
  computed: {
    unsubmitedSids() {
      let sids = _.map(Object.values(this.allStudents), 'id')
      sids = _.difference(sids, this.question.unansweredSids)
      sids = _.difference(sids, _.map(this.question.results, 'sid'))
      return sids
    },
    reviewResults() {
      const arr = []
      for (const r of this.question.results) {
        if (r.status === CM_RECORD_STATUS_REVIEWED) arr.push(r)
      }
      return arr
    },
    unreviewResults() {
      const arr = []
      for (const r of this.question.results) {
        if (r.status === CM_RECORD_STATUS_SUBMITTED) arr.push(r)
      }
      return arr
    },
  },
  methods: {
    toggleResultViewer(result) {
      if (this.viewStudentResult === result) {
        this.viewStudentResult = null
      } else {
        this.viewStudentResult = result
      }
    },
    jumpToNextUnReviewed() {
      for (const result of this.unreviewResults) {
        if (result.sid !== this.viewStudentResult.sid) {
          this.$toasted.success('提交成功，下一位:', { duration: 700, position: 'bottom-right' })
          this.viewStudentResult = result
          return
        }
      }
      this.$toasted.success('本题已全部批改完', { duration: 700, position: 'bottom-right' })
      this.viewStudentResult = null
    },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";

.inline-subjective-result {
  font-family: initial;
}
.note {
  color: #888;
  font-size: .8em;
}
.ui.modal {
  max-height: 90vh;
  .content {
    max-height: 100%;
  }
}

.header.ui {
  border-left: .3rem solid @color-primary;
  padding-left: .5rem;
  font-size: 1.2rem;
  font-weight: normal;
  margin-top: 1rem;
}
[v-cloak] {
  display: none;
}
</style>



