<template>
  <li>
    <div :class="{folder: isFolder}" @click.stop="toggle" v-if="onlyLeafCanSelect && isFolder">
      <span class="toggle-icon">
        <i class="ui icon square outline" :class="node.expanded?'minus':'plus'" ></i>
      </span>
      <span class="node-label">{{node.label}}</span>
    </div>
    <div :class="{folder: isFolder}" @click="nodeClick" v-else>
      <span v-if="isFolder" @click.stop="toggle" class="toggle-icon">
        <i class="ui icon square outline" :class="node.expanded?'minus':'plus'" ></i>
      </span>
      <span class="node-label" :class="selected?'active':''">{{node.label}}</span>
    </div>
    <ul v-show="node.expanded" v-if="isFolder">
      <tree-node
        class="item"
        v-for="child of node.children"
        :key="child.id"
        :node="child"
        :multiSelect="multiSelect"
        :selectedNodes="selectedNodes"
        :onlyLeafCanSelect="onlyLeafCanSelect"
        :mustSelectOne="mustSelectOne">
      </tree-node>
    </ul>
  </li>
</template>

<script>
export default {
  name: 'tree-node',
  props: {
    node: Object,
    multiSelect: { type: Boolean, default: false },
    selectedNodes: { type: Array, required: true },
    mustSelectOne: { type: Boolean, default: false },
    onlyLeafCanSelect: { type: Boolean, default: true },
  },
  data() {
    return {
      active: false,
    }
  },
  beforeCreated() {
    this.$options.components.TreeNode = require('components/tree-node')
  },
  computed: {
    isFolder() {
      return this.node.hasOwnProperty('children') && this.node.children.length >= 0
    },
    selected() {
      return this.selectedNodesIndexOf(this.node) >= 0
    },
  },
  methods: {
    toggle() {
      if (this.isFolder) {
        this.node.expanded = !this.node.expanded
      }
    },
    selectedNodesIndexOf(node) {
      const len = this.selectedNodes.length
      for (let i = 0; i < len; i++) {
        const n = this.selectedNodes[i]
        if (n.id === node.id) return i
      }
      return -1
    },
    nodeClick() {
      const index = this.selectedNodesIndexOf(this.node)
      if (this.selectedNodes.length === 1 && this.mustSelectOne && index >= 0) {
        return
      }

      if (index >= 0) {
        this.selectedNodes.splice(index, 1)
      } else {
        if (!this.multiSelect) {
          this.selectedNodes.length = 0
        }
        this.selectedNodes.push(this.node)
      }
    },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";

.item {
  cursor: pointer;
}
.folder {
  font-weight: bold;
  margin-left: 0;
  margin-top: 3px;
  margin-bottom: 5px;
}
.item .item .folder {
  margin-left: -20px;
}
.item .item ul {
  padding-left: 18px;
}
.ui.icon.square.outline {
  padding-top: 3px;
  padding-bottom: 3px;
  line-height: 1.5;
  margin-right: 0
}
.toggle-icon {
  float: left;
  position: absolute;
  color: #888;
}
.folder .node-label {
  margin-left: 18px;
}
.node-label {
  border: solid 1px transparent;
  border-radius: 2px;
  margin-left: -3px;
  padding: 2px 3px;
  display: inline-block;
}

.node-label.active {
  background-color: @color-blue-50;
  border: solid 1px @color-blue-200;
}

ul {
  padding-left: 36px;
  line-height: 1.5em;
  list-style-type: none;
  display: block;
}

li {
  margin: 0;
  margin-top: 5px;
  margin-bottom: 5px;
  padding: 0 5px 0 0;
  position: relative;
  list-style: none;
  user-select: none;
  display: block;
}

</style>



