<template>
  <div class="edit-question" id="edit-question">
    <p class="ui header" v-if="question">编辑 / {{question.displayName}}</p>
    <p class="ui header" v-else>编辑题目</p>
    <component
      :is="currentView"
      :question="question"
      action="edit"
      v-if="question"
      ></component>
    <div class="content-empty" v-if="!question && errorMsg">
      <img :src="require('assets/img/content-empty.png')">
      <p v-if="!question && errorMsg">{{errorMsg}} 前往<span class="link link-primary" @click="$router.push({name:'manage-questions'})">题目管理列表</span></p>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import axios from 'axios'
import { getModelByDkind } from '@/question-bank/utils'
import { getEditViewByDkind } from '@/question-bank/edit/utils'
import { STATE_EDITING } from '@/question-bank/consts'

export default {
  name: 'edit-question',
  props: {},
  data() {
    return {
      question: null,
      errorMsg: '',
    }
  },
  created() {
  },
  mounted() {
    this.fetchQuestion(this.$route.params.uuid)
  },
  watch: {
  },
  computed: {
    currentView() {
      if (!this.question) return null
      return getEditViewByDkind(this.question.displayKind)
    },
    ...mapState(['teacher']),
  },
  methods: {
    fetchQuestion(uuid) {
      const url = '/question/j/questions/retrieve'
      const params = { uuids: [uuid] }
      axios.get(url, { params }).then((response) => {
        if (response.data.status === 0) {
          if (response.data.questions.length === 0) {
            this.errorMsg = '题目不存在，'
            return
          }
          const questionData = response.data.questions[0]
          if (questionData.authorId !== this.teacher.id) {
            this.errorMsg = '你没有权限编辑此题。'
            return
          }
          if (questionData.state !== STATE_EDITING) {
            this.errorMsg = '此题已发布或者已删除，已不能在进行编辑。'
            return
          }
          const QuestionModel = getModelByDkind(questionData.displayKind)
          this.question = new QuestionModel({
            sourceAction: 'edit',
            ...questionData,
          })
          this.errorMsg = ''
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail)
        }
      })
    },
  },
}
</script>

<style lang="less">
@import "~@/question-bank/edit/index.less";
</style>
<style lang="less" scoped>
.edit-question {
  background-color: white;
  padding: 1em;
  min-height: 80vh;
}

</style>



