import { difference } from 'lodash'
import { BaseQuestion } from './base'
import { Q_ESSAY } from './consts'


class Essay extends BaseQuestion {
  constructor(sourceData) {
    super(sourceData, Q_ESSAY)
    this.structureLevel = 1
    this.stemRich = sourceData.stemRich || {}
    this.analysisRich = sourceData.analysisRich || {}
    this.answerRich = sourceData.answerRich || {}

    this.results = []
    this.unansweredSids = []
    this.windup()
  }

  validate() {
    const error = super.validate()
    return error
  }

  injectReportData(data, allSids) {
    this.results = []
    const answeredSids = []
    for (const sData of data) {
      this.results.push(sData)
      answeredSids.push(sData.sid)
    }
    this.unansweredSids = difference(allSids, answeredSids)
    this.hasReport = true
  }
}

export default Essay



