<template>
    <div class="dkind-switcher" v-if="teacher && selectedDkind">
      <span class="subject-label">{{subjects[teacher.subjectId]}} / </span>
      <div class="ui scrolling selection dropdown">
        <i class="dropdown icon"></i>
        <div class="text">{{kindNameMap[selectedDkind].displayName}}</div>
        <div class="menu">
          <div class="item"
            v-for="dkind of questionDkinds"
            v-if="dkind.displayKind !== selectedDkind"
            @click="$router.push({name: 'create-question', query: {display_kind: dkind.displayKind}})"
            >{{dkind.displayName}}</div>
        </div>
      </div>
    </div>
</template>

<script>
import { SUBJECTS } from 'assets/js/consts/common'
import { Q_DISPLAY_KIND_MAP } from '@/question-bank/consts'
import { mapState } from 'vuex'

export default {
  name: 'dkind-switcher',
  props: {},
  data() {
    return {
      selectedDkind: this.$route.query.display_kind,
      subjects: SUBJECTS,
      kindNameMap: Q_DISPLAY_KIND_MAP,
    }
  },
  components: {},
  mounted() {
    this.$nextTick(() => {
      $('.ui.scrolling.dropdown').dropdown({ action: 'hide' })
    })
  },
  watch: {
    questionDkinds(val) {
      if (val.length <= 0) return
      this.$nextTick(
        () => { $(this.$el).find('.ui.dropdown').dropdown({ action: 'hide' }) },
      )
    },
    '$route.query.display_kind': function (val) {
      this.selectedDkind = val
    }
  },
  computed: {
    ...mapState(['teacher', 'questionDkinds']),
  },
  methods: {
    validateSelectedDkind() {
      if (this.questionDkinds.length <= 0) return false
      for (const item of this.questionDkinds) {
        if (item.displayKind === this.selectedDkind) {
          return true
        }
      }
      return false
    },

  },
}
</script>
<style lang="less" scoped>
@color-blue: #42A5F5;
.dkind-switcher {
  padding-bottom: 1rem;
}
.subject-label {
  font-weight: normal;
  min-width: 4rem;
  font-size: 1.4rem;
}
.ui.scrolling.dropdown {
  max-width: 12rem;
  min-width: 12rem;
  border-radius: 0;
  .text, .dropdown.icon {
    font-weight: bold;
    color: @color-blue;
  }
  .menu {
    right: 0;
  }
}

</style>



