import { STATIC_PREFIX } from 'assets/js/consts/common'
import { BaseQuestion, Option, BaseSubQuestion, ChoiceReportMixin } from './base'
import { Q_LONG_DIALOG, SUBQ_KIND_SINGLE } from './consts'


export class LongDialogQuestion extends BaseSubQuestion {
  constructor(sourceData) {
    super(sourceData)
    this.kind = SUBQ_KIND_SINGLE
    this.textRich = sourceData.textRich || {}
    this.analysisRich = sourceData.analysisRich || {}
    this.answerSeq = sourceData.answerSeq || 0

    this.options = []
    if (sourceData.options) {
      for (const optionData of sourceData.options) {
        optionData.vkey = this.increKey++
        optionData.sourceAction = sourceData.sourceAction
        if (optionData.seq === this.answerSeq) {
          optionData.isAnswer = true
        }
        this.options.push(new Option(optionData))
      }
    }

    if (this.options.length === 0 && this.onEdit) {
      for (let i = 0; i < 4; i++) {
        this.options.push(new Option({ seq: i, vkey: this.increKey++ }))
      }
      this.options[0].isAnswer = true
      this.answerSeq = 0
    }
    Object.defineProperty(
      this, 'answerSeqLabel', { get() { return String.fromCharCode(65 + this.answerSeq) } })
    this.richtextPreparation()
  }
}


export class LongDialog extends BaseQuestion {
  constructor(sourceData) {
    super(sourceData, Q_LONG_DIALOG)
    this.structureLevel = 2
    this.stemRich = sourceData.stemRich || {}
    this.answerSeq = sourceData.answerSeq || 0
    this.audioTextRich = sourceData.audioTextRich || {}
    this.audioPath = sourceData.audioPath || ''
    this.audioPathWrite = sourceData.audioPath || ''

    this.questions = []
    if (sourceData.questions) {
      for (const questionData of sourceData.questions) {
        questionData.vkey = this.increKey++
        questionData.sourceAction = sourceData.sourceAction
        this.questions.push(new LongDialogQuestion(questionData))
      }
    }

    if (this.questions.length === 0 && this.onEdit) {
      this.questions.push(
        new LongDialogQuestion(
          { seq: 1, vkey: this.increKey++, sourceAction: sourceData.sourceAction }
        )
      )
    }

    this.windup()
  }

  get audioUrl() {
    return this.audioPath ? STATIC_PREFIX + this.audioPath : ''
  }

  validate() {
    let error = super.validate()
    if (error) return error
    for (const question of this.questions) {
      error = question.validate()
      if (error) return error
    }
  }
}

Object.assign(LongDialog.prototype, ChoiceReportMixin)

export default LongDialog



