/* eslint-disable no-undef */
const Embed = Quill.import('blots/embed')
import { OSS_ENDPOINT, OSS_BUCKET } from '@/assets/js/consts/common'

const ATTRIBUTES = [
  'alt',
  'height',
  'width',
]
const ossPrefix = `http://${OSS_BUCKET}.${OSS_ENDPOINT}/`

function sanitize(url, protocols) {
  const anchor = document.createElement('a')
  anchor.href = url
  const protocol = anchor.href.slice(0, anchor.href.indexOf(':'))
  return protocols.indexOf(protocol) > -1
}

class Image extends Embed {
  static create(value) {
    const node = super.create(value)
    if (typeof value === 'string') {
      if (this.matchDataUrl(value)) {
        node.setAttribute('src', this.sanitize(value))
      } else if (this.matchUrl(value)) {
        node.setAttribute('src', this.sanitize(ossPrefix + value))
      }
    }
    return node
  }

  static formats(domNode) {
    return ATTRIBUTES.reduce(function (formats, attribute) {
      if (domNode.hasAttribute(attribute)) {
        formats[attribute] = domNode.getAttribute(attribute)
      }
      return formats
    }, {})
  }

  static matchDataUrl(url) {
    return /^data:image\/.+;base64/.test(url)
  }

  static matchUrl(url) {
    return /\.(jpe?g|gif|png)$/.test(url)
  }

  static sanitize(url) {
    return sanitize(url, ['http', 'https', 'data']) ? url : '//:0'
  }

  static value(domNode) {
    const src = domNode.getAttribute('src')
    if (this.matchDataUrl(src)) {
      return src
    }
    if (this.matchUrl(src)) {
      const src_ = src.slice(src.indexOf(':') + 1)
      return src_.replace(`//${OSS_BUCKET}.${OSS_ENDPOINT}/`, '')
    }
  }

  format(name, value) {
    if (ATTRIBUTES.indexOf(name) > -1) {
      if (value) {
        this.domNode.setAttribute(name, value)
      } else {
        this.domNode.removeAttribute(name)
      }
    } else {
      super.format(name, value)
    }
  }
}

Image.blotName = 'image'
Image.tagName = 'IMG'


export default Image



