<template>
  <div class="common-info">
    <div class="qrow">
      <span class="qrow-label required">难度</span>
      <div class="qrow-content">
        <span class="ui radio checkbox" @click="question.difficulty=1">
          <input type="radio" :checked="question.difficulty===1">
          <label>简单</label>
        </span>
        <span class="ui radio checkbox" @click="question.difficulty=3">
          <input type="radio" :checked="question.difficulty===3">
          <label>中等</label>
        </span>
        <span class="ui radio checkbox" @click="question.difficulty=5">
          <input type="radio" :checked="question.difficulty===5">
          <label>困难</label>
        </span>
      </div>
    </div>
    <div class="qrow">
      <span class="qrow-label required">章节</span>
      <div class="qrow-content">
        <span class="ui chapter label" v-for="chapter of question.chapters">
          {{chapter.label}}
          <span class="action-icon" @click="removeChapter(chapter)"><icon name="slim-remove"></icon></span>
        </span>
        <button class="link link-primary" @click="openChapterModal=true"><i class="edit icon"></i>选择</button>
      </div>
    </div>
    <chapter-tag-modal
      :open="openChapterModal"
      :selectedChapters="question.chapters"
      @close="openChapterModal=false"
    ></chapter-tag-modal>
    <div class="qrow">
      <span class="qrow-label required">知识点</span>
      <div class="qrow-content">
        <span class="ui knowledge label" v-for="k of question.knowledges">
          {{k.label}}
          <span class="action-icon" @click="removeKnowledge(k)"><icon name="slim-remove"></icon></span>
        </span>
        <button class="link link-primary" @click="openKnowledgeModal=true"><i class="edit icon"></i>选择</button>
      </div>
    </div>
    <knowledge-tag-modal
      :open="openKnowledgeModal"
      :selectedKnowledges="question.knowledges"
      @close="openKnowledgeModal=false"
    ></knowledge-tag-modal>
    <div class="qrow">
      <span class="qrow-label">来源</span>
      <div class="qrow-content">
        <input type="text" class="input-cross" v-model="question.source">
      </div>
    </div>
  </div>
</template>

<script>
import Modal from 'components/modal'
import ChapterTagModal from 'components/chapter-tag-modal'
import KnowledgeTagModal from 'components/knowledge-tag-modal'
import Icon from 'components/icons/Icon'
import 'components/icons/slim-remove'

export default {
  name: 'common-info',
  props: {
    question: { type: Object, required: true },
  },
  data() {
    return {
      openChapterModal: false,
      openKnowledgeModal: false,
    }
  },
  components: { Modal, Icon, ChapterTagModal, KnowledgeTagModal },
  computed: {},
  methods: {
    removeChapter(chapter) {
      const index = this.question.chapters.indexOf(chapter)
      if (index >= 0) {
        this.question.chapters.splice(index, 1)
      }
    },
    removeKnowledge(knowledge) {
      const index = this.question.knowledges.indexOf(knowledge)
      if (index >= 0) {
        this.question.knowledges.splice(index, 1)
      }
    },
  },
}
</script>

<style lang="less" scoped>
.common-info .qrow {
  .qrow-label {
    width: 5em;
  }
  .input-cross {
    max-width: 30rem;
  }
}
</style>



