<!-- 用于较多内容和交互的modal展示，关闭图标在 dim warpper 的右上角。 -->
<template>
 <transition name="modal">
    <div class="page-modal modal-mask" @click.self="clickModalMask()">
      <button class="ui tiny icon dismiss-modal button" @click="$emit('close')" v-if="showCloseIcon">
        <icon name="slim-remove"></icon>
      </button>
      <div class="modal-container">
        <slot name="content"></slot>
      </div>
    </div>
  </transition>
</template>

<script>
/*
样式(class)选择：modal-large, modal-massive, modal-small
尽量不要嵌套。
*/

import 'components/icons/slim-remove'
import Icon from 'components/icons/Icon'

export default {
  name: 'page-modal',
  props: {
    escBinding: { type: Boolean, default: true },
    showCloseIcon: { type: Boolean, default: true },
    maskCloseBinding: { type: Boolean, default: true },
  },
  data() {
    return {
      scrollbarWidth: window.innerWidth - document.body.clientWidth,
      originalBodyPad: document.body.style.paddingRight || '',
      bodyPad: parseInt((document.body.style.paddingRight || 0), 10),
    }
  },
  components: { Icon },
  mounted() {
    document.body.classList.add('page-modal-open')
    document.body.style.paddingRight = `${this.scrollbarWidth + this.bodyPad}px`
    if (this.escBinding) window.addEventListener('keyup', this.closeSelf)
  },
  beforeDestroy() {
    if (this.escBinding) window.removeEventListener('keyup', this.closeSelf)
  },
  destroyed() {
    this.$el.style.display = 'none'
    if (document.querySelectorAll('.page-modal').length === 1) {
      document.body.classList.remove('page-modal-open')
    }
    document.body.style.paddingRight = this.originalBodyPad
  },
  methods: {
    clickModalMask() {
      if (this.maskCloseBinding) this.$emit('close')
    },
    closeSelf(e) {
      if (e.keyCode === 27) {
        this.$emit('close')
      }
    },
  },
}
</script>

<style lang="less">
body.page-modal-open {
  overflow: hidden;
}
.page-modal.modal-mask {
  justify-content: flex-start;
}

.modal-container {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}
</style>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";

.page-modal.modal-mask {
  font-size: 14px;
  position: fixed;
  z-index: 2000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity .3s ease;
  display: flex;
  align-items: center;
  padding: 1rem;
  overflow-y: scroll;
  overflow-x: auto;
  flex-direction: column;

  .dismiss-modal.button {
    position: fixed;
    top: 10px;
    right: 15px;
    padding: 0;
    background-color: transparent !important;
    color: #ddd;
    font-size: 1.1rem;
  }
}

.modal-container {
  min-width: 450px;
  width: 700px;
  margin: 40px auto;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .4s ease;
  border-radius: .2rem;
  display: block;
}

.modal-full .modal-container {
  width: 800px;
}
.modal-massive .modal-container {
  width: 800px;
}
.modal-large .modal-container {
  width: 800px;
}
.modal-small .modal-container {
  width: 400px;
}

.modal-padded .modal-container {
  padding: 2rem 1.5rem;
}

@media (min-width: @max-width)
{
  .modal-full .modal-container {
    width: @max-width;
  }
  .modal-massive .modal-container {
    width: @max-width - 100px;
  }
  .modal-large .modal-container {
    width: 1100px;
  }
  .modal-container {
    width: 850px
  }
  .modal-small .modal-container {
    width: 500px;
  }
}

@media (min-width: 1300px) and (max-width: @max-width)
{
  .modal-full .modal-container {
    width: 95vw;
  }
  .modal-massive .modal-container {
    width: 90vw;
  }
  .modal-large .modal-container {
    width: 1000px;
  }
  .modal-container {
    width: 850px
  }
  .modal-small .modal-container {
    width: 500px;
  }
}

@media (min-width: 1000px) and (max-width: 1300px)
{
  .modal-full .modal-container {
    width: 95vw;
  }
  .modal-massive .modal-container {
    width: 90vw;
  }
  .modal-large .modal-container {
    width: 900px;
  }
  .modal-container {
    width: 700px
  }
  .modal-small .modal-container {
    width: 400px;
  }
}
.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}
</style>



