<template>
  <page-modal
    v-if="question && question.hasReport && allStudents"
    @close="$emit('close')"
    class="modal-large">
    <div slot="content" class="modal-objective-results">
      <div class="header">
        <h3 class="title">作答详情</h3>
        <span class="text-small">
          {{wrongSids.size}}人错误，{{rightSids.size}}人正确，{{unansweredSids.size}}人未作答
        </span>
      </div>

      <div class="section">
        <template
          v-for="option of question.options"
          v-if="option.sids.length">
          <h4 class="title">选 <span :class="option.isAnswer && 'option-correct' || 'option-wrong'">{{option.seqLabel}}</span> <span class="text-muted">{{option.sids.length}}人 /{{Math.round(100 * option.sids.length/totalStuCount)}}%</span></h4>
          <student-labels
            :allStudents="allStudents"
            :orderedSids="option.sids"></student-labels>
        </template>
        <div class="content-empty" v-if="!answeredSids.size">
          <img :src="require('assets/img/content-empty.png')">
          <p>本题还没有人作答</p>
        </div>
      </div>
    </div>
  </page-modal>
</template>

<script>
import PageModal from 'components/page-modal'
import StudentLabels from 'components/student-labels'

export default {
  name: 'modal-objective-results',
  props: {
    // 一般这里是指小题
    question: { required: true },
    allStudents: { required: true },
  },
  data() {
    const wrongSids = new Set()
    const rightSids = new Set()
    const answeredSids = new Set()
    const unansweredSids = new Set()
    for (const option of this.question.options) {
      for (const sid of option.sids) {
        if (!option.isAnswer) {
          wrongSids.add(sid)
        }
        answeredSids.add(sid)
      }
    }
    for (const sidStr in this.allStudents) {
      const sid = parseInt(sidStr)
      if (answeredSids.has(sid) && !wrongSids.has(sid)) rightSids.add(sid)
      if (!answeredSids.has(sid)) unansweredSids.add(sid)
    }
    return {
      wrongSids,
      rightSids,
      unansweredSids,
      answeredSids,
    }
  },
  components: { PageModal, StudentLabels },
  computed: {
    totalStuCount() {
      if (!this.allStudents) return 0
      return Object.keys(this.allStudents).length
    },
  },
}
</script>

<style lang="less" scoped>
@import '~assets/css/base-variables.less';

.modal-objective-results {
  .header {
    padding: .7em 2em;
    background-color: @color-blue;
    color: white;
    border-bottom: 1px solid #eee;
    .title {
      margin: 0;
      font-size: 1.3rem;
    }
  }
  .section {
    min-height: 22em;
    padding: .4em 2em 2em 2em;
    .title {
      font-weight: normal;
      font-size: 1.1rem;
      margin: 1.2em 0 .3em 0;
    }
    .content-empty {
      margin-top: 3rem;
      img {
        max-width: 80px;
      }
    }
  }
}
.option-correct {
  color: @color-teal;
}
.option-wrong {
  color: @color-red;
}

.text-muted {
  margin-left: 1em;
  color: #909baa;
  font-size: .9em;
}

</style>



