import { BaseQuestion, Option, ChoiceReportMixin } from './base'
import { Q_MULTI_CHOICE } from './consts'

class MultiChoice extends BaseQuestion {
  constructor(sourceData) {
    super(sourceData, Q_MULTI_CHOICE)
    this.structureLevel = 1
    this.stemRich = sourceData.stemRich || {}
    this.analysisRich = sourceData.analysisRich || {}

    this.options = []
    if (sourceData.options) {
      for (const optionData of sourceData.options) {
        this.options.push(new Option({
          multiAnswer: true,
          vkey: this.increKey++,
          ...optionData,
        }))
      }
    }

    if (this.options.length === 0 && this.onEdit) {
      for (let i = 0; i < 4; i++) {
        this.options.push(new Option({ seq: i, vkey: this.increKey++ }))
      }
    }

    this.windup()
  }

  validate() {
    const errors = super.validate()
    return errors
  }

  get answerSeqComposed() {
    return this.options.filter(option => option.isAnswer).map(option => String.fromCharCode(65 + option.seq)).join(' ')
  }
}

Object.assign(MultiChoice.prototype, ChoiceReportMixin)

export default MultiChoice



