import { BaseQuestion, BaseSubQuestion,  } from './base'
import { parseRichProperties } from './richtext'
import { Q_OBJECTIVE_BLANK, SUBQ_KIND_BLANK } from './consts'


export class ObjectiveBlankQuestion extends BaseSubQuestion {
  constructor(sourceData) {
    super(sourceData)
    this.kind = SUBQ_KIND_BLANK
    this.analysisRich = sourceData.analysisRich || {}
    this.answerComposed = sourceData.answerComposed || ''
    this.userAnswers = {}

    parseRichProperties(this)
  }

  get answers() {
    return this.answerComposed.split('$$')
      .map(answer => answer.trim())
      .filter(answer => answer.length > 0)
  }

  getUserAnswer(studentId) {
    if (studentId in this.userAnswers) {
      return this.userAnswers[studentId]
    }
    return ''
  }

  isUserCorrect(studentId) {
    return this.answers.includes(this.getUserAnswer(studentId))
  }

  validate() {
    this.answerComposed = this.answers.join('$$')
    if (this.answers.length <= 0) return [false, '请补全答案']
  }
}


export class ObjectiveBlank extends BaseQuestion {
  constructor(sourceData) {
    super(sourceData, Q_OBJECTIVE_BLANK)
    this.structureLevel = 2
    this.stemRich = sourceData.stemRich || {}

    this.questions = []
    if (sourceData.questions) {
      for (const questionData of sourceData.questions) {
        questionData.vkey = this.increKey++
        questionData.sourceAction = sourceData.sourceAction
        this.questions.push(new ObjectiveBlankQuestion(questionData))
      }
    }
    this.windup(this)
  }

  validate() {
    const error = super.validate()
    if (error) return error
  }

  injectReportData(reportData) {
    for (const subq of this.questions) {
      const userAnswers = reportData[subq.id]
      subq.correctSids = []
      subq.wrongResults = []
      const standardAnswers = subq.answers

      for (const sid in userAnswers) {
        const userAnswer = userAnswers[sid].trim()
        if (!userAnswer) {
          continue
        }
        if (standardAnswers.includes(userAnswer)) {
          subq.correctSids.push(sid)
        } else {
          subq.wrongResults.push({ sid, answer: userAnswer })
        }
        userAnswers[sid] = userAnswer
      }
      subq.userAnswers = reportData[subq.id]
      subq.hasReport = true
    }
    this.hasReport = true
  }
}

export default ObjectiveBlank



